import eventRegistry from "./event-registry";

import HstRequest from "./hstRequest/request";
import { EventResult } from "./enums"
import logger from "./logger"

let hstRequest = new HstRequest()
// websocket 
hstRequest.interceptors.response.use(({data, config}) => {
  // 
  if (data === EventResult.NOADDR && config.retryConfig.num < config.retryConfig.max) {
    config.retryConfig.num ++
    logger.warn(`start retry receive media: ${config.retryConfig.num}`)
    return hstRequest.request(config)
  }
  return data
}, (err) => {
})

// Promise
const promiseHandler = function(eventName, fn) {
  return new Promise((resolve, reject) => {
    //FIXMEonce
    eventRegistry.on(eventName, function() {
      // 
      let result = Array.prototype.shift.call(arguments);
      if (parseInt(result) === 0) {
        fn && fn.call(this);
        resolve(...arguments);
      } else {
        reject(...arguments);
      }
    });
  });
};

export {
  promiseHandler,
  hstRequest
}
