import Store from "./store"
import { generateGUID } from "../../common/common"
import { MediaType, RecvMagicMode } from "../../common/enums"
import audioManager from "../media/audio-manager"

class ProtocolSender {
  sendJoinGroupReq(groupId) {
    let msg = {
      business: "GS",
      id: 14100,
      group_id: groupId
    };
    msg = this._dealMsg(msg)
    Store.websocket.send(JSON.stringify(msg));
  }
  
  sendLeaveGroupReq() {
    let msg = {
      business: "GS",
      id: 14102
    };
    msg = this._dealMsg(msg)
    Store.websocket.send(JSON.stringify(msg));
  }
  
  sendResolutionReport(params) {
    let msg = {
      business: "SS",
      id: 18004,
      stream_id: params.streamId,
      width: params.width,
      height: params.height,
      channel_type: params.channelType,
      report_type: params.reportType
    };
    msg = this._dealMsg(msg)
    Store.websocket.send(JSON.stringify(msg));
  }
  
  sendLogoutReq(){
    let msg = {
      business: "BASE",
      id: 10002
    };
    msg = this._dealMsg(msg)
    Store.websocket.send(JSON.stringify(msg));
  }
  
  sendLoginReq(options){
    let msg = {
      business: "BASE",
      id: 10000,
      app_id: options.appId,
      company_id: options.companyId,
      token: options.token,
      user_id: options.userId,
      client_guid: generateGUID(), // FIXME
      os_type: options.osType,
      mutex_type: options.mutexType,
      force_login: options.forceLogin, 
      registered_user: options.registered,
      protocol_version: "0.0.1",
      sdk_type: 26,
      extend_info: options.extendInfo,
    };
    msg = this._dealMsg(msg)
    Store.websocket.send(JSON.stringify(msg));
  }
  
  sendPublishMediaReq(operation, mediaType, mediaId, mediaOwner){
    let msg = {
      business: "GS",
      id: 14104,
      //012
      media_type: mediaType,
      // TODO MediaId
      media_id: mediaId,
      media_owner: mediaOwner,
      //01
      operation: operation
    };
  
    msg = this._dealMsg(msg)
    Store.websocket.send(JSON.stringify(msg));
  }
  
  sendRecvMediaReq(recvState, userId, mediaType, mediaId) {
    let msg = {
      business: "SS",
      id: 18002,
      group_id: Store.userInfo.groupId,
      user_id: userId,
      media_type: mediaType, //012
      media_id: mediaId,
      recv: recvState       //01
    };
  
    if (mediaType == MediaType.AUDIO) {
      // media_id_magic
      if (audioManager.recvMagicAudio == RecvMagicMode.ONLY_MAGIC) {
        if (mediaId.indexOf("_magic") == -1) {
          msg.media_id = mediaId + "_magic";
        }  
      } else if (audioManager.recvMagicAudio == RecvMagicMode.ONLY_ORIGIN) { // media_id_magic
        if (mediaId.indexOf("_magic") != -1) {
          msg.media_id = mediaId.substr(0, mediaId.length - 6);
        }
      } else {
        // Do nothing
      }
    }

    // 
    if (mediaType == MediaType.WHITE_BOARD) {
      msg.media_owner = "group"
    } else {
      msg.media_owner = "user"
    }
  
    msg = this._dealMsg(msg)
    Store.websocket.send(JSON.stringify(msg));
  }
  
  sendRecvWhiteboardReq(recvState, mediaType, mediaId) {
    let msg = {
      business: "SS",
      id: 18002,
      group_id: Store.userInfo.groupId,
      media_type: mediaType, //012, 4
      media_id: mediaId,
      recv: recvState,       //01
      source_type: 1,
      user_data: {}
    };

    msg = this._dealMsg(msg)
    Store.websocket.send(JSON.stringify(msg));
  }
  
  sendCreateWhiteboardReq(parmas) {
    let msg = {
      business: "WBS",
      id: 19000,
      cli_seq_id: 1,
      board_type: 0, 
      width: 1280, 
      height: 768,
      file_path: '',
      convert_file_path: '',
      page: 1
    }
    msg = Object.assign(msg, parmas)
    msg.convert_file_path = msg.convert_file_path.toUpperCase()
    msg = this._dealMsg(msg)
    Store.websocket.send(JSON.stringify(msg));
  }
  
  sendCloseWhiteboardReq(mediaId) {
    let msg = {
      business: "WBS",
      id: 19002,
      board_id: mediaId
    }
    msg = this._dealMsg(msg)
    Store.websocket.send(JSON.stringify(msg));
  }

  sendOpenUpload ({ file_path, size }) {
    let msg = {
      business: 'STORE',
      id: 20010,
      upload_id: Store.userInfo.userId,
      checksum: '',
      file_path: file_path,
      file_size: size,
      upload_mode: 0
    }
    msg = this._dealMsg(msg)
    Store.websocket.send(JSON.stringify(msg))
  }

  sendUploadEnd (params) {
    let msg = {
      business: 'STORE',
      id: 20002,
      ...params
    }
    msg = this._dealMsg(msg)
    Store.websocket.send(JSON.stringify(msg))
  }
  sendTransCodeMsg (file_path) {
    let msg = {
      business: 'DC',
      id: 21000,
      file_path: file_path
    }
    msg = this._dealMsg(msg)
    Store.websocket.send(JSON.stringify(msg))
  }
  
  sendNotifySendStreamRes(params) {
    let msg = {
      business: "SS",
      id: 18001,
      stream_id: params.streamId,
      send: params.send,
      result: params.result
    };
    msg = this._dealMsg(msg)
    Store.websocket.send(JSON.stringify(msg));
  } 

  sendLogoutRecvChannelReq(streamId) {
    let msg = {
      business: 'SS',
      id: 19012,
      stream_id: streamId,
      client_id: "{" + Store.userInfo.appId + ";" + Store.userInfo.groupId + "};" + Store.userInfo.userId
    }
    msg = this._dealMsg(msg)
    Store.websocket.send(JSON.stringify(msg));
  }

  sendLoginRecvChannelReq(params, bindId) {
    let msg = {
      business: 'WBS',
      id: 19004,
      stream_id: params.streamId,
      stream_token: params.streamToken,
      media_type: params.media_type,
      media_id: params.media_id,
      client_id: "{" + Store.userInfo.appId + ";" + Store.userInfo.groupId + "};" + Store.userInfo.userId,
      version: "1.0.0",
      bind_id: bindId
    }
    msg = this._dealMsg(msg, params.channelId)
    Store.websocket.send(JSON.stringify(msg));
  }

  sendSdpInfo(description, pcId) {
    let msg = {
      business: "SS",
      id: 19008,
      type: description.type,
      pc_id: pcId,
      sdp: description.sdp
    };
    msg = this._dealMsg(msg)
    Store.websocket.send(JSON.stringify(msg));
  }

  sendCandidate(event, pcId) {
    let msg = {
      business: "SS",
      id: 19008,
      pc_id: pcId,
      candidate: event.candidate.candidate,
      sdpMid: event.candidate.sdpMid,
      sdpMLineIndex: event.candidate.sdpMLineIndex
    };
    msg = this._dealMsg(msg)
    Store.websocket.send(JSON.stringify(msg));
  }

  sendLoginSendChannelReq(params, bindId) {
    let msg = {
      business: 'WBS',
      id: 19006,
      stream_id: params.streamId,
      stream_token: params.streamToken,
      media_type: params.mediaType,
      media_id: params.mediaId,
      client_id: Store.getClientId(),
      version: "1.0.0",
      bind_id: bindId
    }
    msg = this._dealMsg(msg)
    Store.websocket.send(JSON.stringify(msg));
  }

  sendSetAudioMagic(streamId, value) {
    let msg = {
      business: "SS",
      id: 19010,
      stream_id: streamId,
      client_id: Store.getClientId(),
      param: [{
        param_type: "SoundTouch",
        param_value: value.toString()
      }]
    };
    msg = this._dealMsg(msg)
    Store.websocket.send(JSON.stringify(msg));
  }

  SendSetAudioMagicReq(streamId, value) {
    let msg = {
      business: "SS",
      id: 19010,
      stream_id: streamId,
      client_id: Store.getClientId(),
      param: [{
        param_type: "SoundTouch",
        param_value: value.toString()
      }]
    };
    msg = this._dealMsg(msg)
    Store.websocket.send(JSON.stringify(msg));
  }

  sendGetWhiteBoardFullDataReq(channelId) {
    let msg = {
      id: 30003
    };
    msg = this._dealMsg(msg, channelId)
    Store.websocket.send(JSON.stringify(msg));
  }

  sendChangeWhiteBoardProperty(params, channelId) {
    let msg = {
      id: 30103,
      seq_id: Math.floor(Math.random() * 1000),
      edit_id: params.edit_id,
      zoom: params.zoom,
      rotate_angle: params.rotateAngle,
      mouse_posx: params.mouse_posx,
      mouse_posy: params.mouse_posy,
      scroll_posx: params.scroll_posx,
      cur_page: params.cur_page
    };
    msg = this._dealMsg(msg, channelId)
    Store.websocket.send(JSON.stringify(msg))
  }

  sendSwitchWhiteBoard(editId ,channelId) {
    let msg = {
      id: 30101,
      seq_id: Math.floor(Math.random() * 100),
      edit_id: editId,
      is_select: true
    }
    msg = this._dealMsg(msg, channelId)
    Store.websocket.send(JSON.stringify(msg))
  }

  sendRecvBoardFileList(filePath) {
    let msg = {
      business: 'STORE',
      id: 20004,
      path: filePath,
    }
    msg = this._dealMsg(msg)
    Store.websocket.send(JSON.stringify(msg))
  }

  sendGetWhiteBoardFileUrlReq(filePath) {
    let msg = {
      business: 'STORE',
      id: 20006,
      file_path: filePath
    }
    msg = this._dealMsg(msg)
    Store.websocket.send(JSON.stringify(msg));
  }

  sendUserMsg(params) {
    let msg = {
      business: "SG",
      id: 11000,
      dst_user_id: params.dstUserId,
      msg_id: 1024,
      msg: params.msg
    };
    msg = this._dealMsg(msg)
    Store.websocket.send(JSON.stringify(msg));
  }

  sendUserMsgAck(params) {
    let msg = {
      business: "SG",
      id: 11004,
      src_user_id: params.src_user_id,
      msg_id: params.msg_id
    };
    msg = this._dealMsg(msg)
    Store.websocket.send(JSON.stringify(msg));
  }

  sendGroupMsgWithWhiteList(params) {
    let msg = {
      business: "SG",
      id: 11006,
      group_id: params.groupId,
      white_list: params.whiteList,
      msg_id: 1024,
      msg: params.msg
    };
    msg = this._dealMsg(msg)
    Store.websocket.send(JSON.stringify(msg));
  }

  sendGroupMsgWithBlackList(params) {
    let msg = {
      business: "SG",
      id: 11006,
      group_id: params.groupId,
      black_list: params.blackList,
      msg_id: 1024,
      msg: params.msg
    };
    msg = this._dealMsg(msg)
    Store.websocket.send(JSON.stringify(msg));
  }

  sendGroupMsg(params) {
    let msg = {
      business: "SG",
      id: 11006,
      group_id: params.groupId,
      msg_id: 1024,
      msg: params.msg
    };
    msg = this._dealMsg(msg)
    Store.websocket.send(JSON.stringify(msg));
  }

  sendGroupMsgAck(params) {
    let msg = {
      business: "SG",
      id: 11010,
      group_id: params.group_id,
      src_user_id: params.src_user_id,
      msg_id: params.msg_id
    };
    msg = this._dealMsg(msg)
    Store.websocket.send(JSON.stringify(msg));
  }

  sendKickOutRes() {
    let msg = {
      business: "PS",
      id: 12008
    };
    msg = this._dealMsg(msg)
    Store.websocket.send(JSON.stringify(msg));
  }

  /**
   * 
   * @param {*} websocket 
   */
   sendOpenEdit (channelId) {
    let msg = {
      id: 30001
    }
    msg = this._dealMsg(msg, channelId)
    Store.websocket.send(JSON.stringify(msg))
  }
  sendPageInfoMsg (params) {
    let msg = {
      id: 30200,
      page: {
        pid: params.curPage,
        action: params.action
      },
      edit_id: params.edit_id,
      seq_id: params.seq_id,
      type: 'page',
    }
    msg = this._dealMsg(msg, params.channelId)
    Store.websocket.send(JSON.stringify(msg))
  }

  sendGraphMsg (parmas) {
    // let msg = Object.assign({id: 31000}, parmas)
    let msg = this._dealMsg(parmas, parmas.channelId)
    Store.websocket.send(JSON.stringify(msg))
  }

  _dealMsg(msg, channelId) {
    if (!channelId) {
      channelId = Store.mainWebsocketChannelId
    }
    return {
      id: 102,
      channel_id: channelId,
      msg: JSON.stringify(msg),
    }
  }
}

export default new ProtocolSender();