
import Store from "../util/store"
import protocolSender from "../util/protocol-sender"
import { MediaType, MediaOper, EventType, RecvState, DisplayMode, EventResult } from "../../common/enums"
import { tracelog, asyncWrapper } from '../../common/common'
import { promiseHandler, hstRequest } from "../../common/promise-handler"
import logger from '../../common/logger'
import { getEventPostfix } from '../../common/common'
import Painter from "./whiteBoard/Painter"
import eventRegistry from "../../common/event-registry"
import UploaderManager  from './upload-manager'

/**
 * 
 */
class WhiteboardManager {
  constructor() {
    this._init();

    // 
    Store.msgDispatcher.addMsgHandler(19001, this._onCreateWhiteBoardRes.bind(this));
    Store.msgDispatcher.addMsgHandler(19003, this._onCloseWhiteBoardRes.bind(this));
    Store.msgDispatcher.addMsgHandler(20005, this._onNotifyGetFileList.bind(this));
    Store.msgDispatcher.addMsgHandler(20007, this._onGetWhiteBoardFileUrlRes.bind(this));
    Store.msgDispatcher.addMsgHandler(30101, this._onNotifySwitchWhiteBoardRes.bind(this));

    // 
    eventRegistry.on("restoreWhiteBoard", this._onRestoreWhiteBoard.bind(this));

    return this;
  }

  /**
   * 
   * @param {*} mediaId
   * @returns  
   */
  @tracelog
  tryRestoreWhiteBoardRender(mediaId) {
    if (this.restoreWhiteBoards.length == 0) {
      logger.log("Empty restore white boards.")
      return false;
    }

    let board = this.restoreWhiteBoards[0];
    if (board.mediaId != mediaId) {
      logger.log("Different white board.");
      return false;
    }

    this.setWhiteBoardRender(mediaId, board.parentEle);
    this.setDisplayMode(mediaId, board.displayMode, board.displayParam);

    this.restoreWhiteBoards.shift();
    if (this.restoreWhiteBoards.length > 0) {
      let board = this.restoreWhiteBoards[0]
      asyncWrapper(()=>{this._doStartReceiveWhiteBoard(board.userId, board.mediaId)})
      console.warn("Try to restore white board: " + board.mediaId)
    }

    return true;
  }

  /**
   * 
   * @param {*} mediaId 
   */
  _doStartReceiveWhiteBoard(userId, mediaId) {
    this.startReceiveWhiteBoard(userId, mediaId).then(()=>{
      logger.log("Start receive white board success.");
    }).catch((e)=>{
      logger.error("Start receive white board failed, err: " + e);
      this.restoreWhiteBoards.shift();
    })
  }

  /**
   * 
   * @param {*} params 
   */
  @tracelog
  _onRestoreWhiteBoard(params) {
    this.restoreWhiteBoards.push(params);
    if (this.restoreWhiteBoards.length == 1) {
      this._doStartReceiveWhiteBoard(params.userId, params.mediaId);
    }
  }
  

  _onNotifySwitchWhiteBoardRes(params) {
    const { channel_id } = params
    const selectMedia = this.whiteBoards.find(item => { return item.channelId === channel_id })
    selectMedia._updateMsgSeq(params)
  }

  /**
   * 
   */
  _init() {
    // 
    this.whiteBoards = new Array();

    // 
    this.displayMode = null;
    this.displayParam = null;

    // fileUrlMap
    this.cacheFileUrlMap = new Map()
    this.cacheTempPathMap = new Map()

    // 
    this.restoreWhiteBoards = new Array();
  }

  /**
   * 
   */
  createWhiteBoard(params) {
    const path = params.convert_file_path
    if (path) {
      params.board_type = 1
      params.convert_file_path = path.substr(0, path.lastIndexOf('/'))
    }
    protocolSender.sendCreateWhiteboardReq(params);
    return promiseHandler(EventType.EVENT_CREATE_WHITE_BOARD).catch(err=> {
      console.error('createWhiteBoard err', err)
    })
}
  
  /**
   * 
   * @param {*} params 
   */
  _onCreateWhiteBoardRes(params) {
    if (params.result === 0) {
      this.startPublishWhiteBoard(params.board_id)
      eventRegistry.trigger(
        EventType.EVENT_CREATE_WHITE_BOARD, 
        EventType.EVENT_CREATE_WHITE_BOARD, 
        EventType.EVENT_CREATE_WHITE_BOARD, 
        params.result == 0 ? EventResult.SUCCESS : EventResult.FAILED)
    }
  }

  /**
   * 
   * @param {*} mediaId 
   */
  closeWhiteBoard(mediaId) {
    if (!mediaId) {
      console.error("Invalid mediaId!");
      return;
    }
    
    protocolSender.sendCloseWhiteboardReq(mediaId)
    return promiseHandler(EventType.EVENT_CLOSE_WHITE_BOARD);
  }

  rotateWhiteBoard(rotateAngle, mediaId) {
    const board = this.whiteBoards.find(item => { return item.mediaId === mediaId })
    if (board) {
      board.rotate(rotateAngle)
    }
  }

  switchWhiteBoard(mediaId) {
    const board = this.whiteBoards.find(item =>item.mediaId === mediaId)
    if (board) {
      protocolSender.sendGetWhiteBoardFullDataReq(board.channelId)
      board.switchBoard()
    }
  }

  getFileList(filePath) {
    protocolSender.sendRecvBoardFileList(filePath)
  }

  switchWhiteBoardPage(curPage, mediaId, needNotifyOthers) {
    const board = this.whiteBoards.find(item => { return item.mediaId === mediaId })
    if (board) {
      board.switchBoardPage(curPage, needNotifyOthers)
    }
  }

  /**
   * 
   * @param {*} params 
   */
  _onCloseWhiteBoardRes(params) {
    eventRegistry.trigger(
      EventType.EVENT_CREATE_WHITE_BOARD, 
      params.result == 0 ? EventResult.SUCCESS : EventResult.FAILED)
  }

  /**
   * 
   * @param {*} mediaId 
   */
  startPublishWhiteBoard(mediaId) {
    protocolSender.sendPublishMediaReq(MediaOper.START_PUBLISH, MediaType.WHITE_BOARD, mediaId, 'group');
    
    // 
    // return promiseHandler(EventType.EVENT_START_PUB_WHITE_BOARD);
  }

  /**
   * 
   * @param {*} mediaId 
   */
  stopPublishWhiteBoard(mediaId) {
    protocolSender.sendPublishMediaReq(MediaOper.STOP_PUBLISH, MediaType.WHITE_BOARD, mediaId, 'group');
    
    // 
    // return promiseHandler(EventType.EVENT_STOP_PUB_WHITE_BOARD);
  }

  /**
   * 
   * @param {*} userId 
   * @param {*} mediaId 
   */
  startReceiveWhiteBoard(userId, mediaId) {
    return hstRequest.request({
      eventName: EventType.EVENT_START_RCV_MEDIA + getEventPostfix(userId, MediaType.WHITE_BOARD, mediaId),
      retryConfig: {
        num: 0,
        max: 4
      },
      requestFn: () => {
        protocolSender.sendRecvMediaReq(
          RecvState.START_RECV, 
          userId, 
          MediaType.WHITE_BOARD,
          mediaId);
      }
    })
    // protocolSender.sendRecvMediaReq(
    //   RecvState.START_RECV, 
    //   userId, 
    //   MediaType.WHITE_BOARD, 
    //   mediaId);

    // // 
    // return promiseHandler(EventType.EVENT_START_RCV_MEDIA + getEventPostfix(userId, MediaType.WHITE_BOARD, mediaId));
  }

  /**
   * 
   * @param {*} userId 
   * @param {*} mediaId 
   */
  stopReceiveWhiteBoard(userId, mediaId) {
    // 
    const board = this.whiteBoards.find(board=> { return board.mediaId === mediaId })
    if (board && board.pageStore) {
      const path = board.pageStore.getConvertFilePath()
      if (path) {
        this.cacheFileUrlMap.delete(path)
      }
    }
    // 
    setTimeout(()=> {
      protocolSender.sendRecvMediaReq(
        RecvState.STOP_RECV, 
        userId, 
        MediaType.WHITE_BOARD, 
        mediaId)
    }, 100)

    return promiseHandler(EventType.EVENT_STOP_RCV_MEDIA + getEventPostfix(userId, MediaType.WHITE_BOARD, mediaId));
  }

  /**
   * 
   * @param {*} mediaId 
   */
   startUploadDoc(params) {
    UploaderManager.startUpload(params)
  }

  /**
   * 
   * @param {*} mediaId 
   * @param {*} parentEle 
   */
  setWhiteBoardRender(mediaId, renderEle, params) {
    for (let board of this.whiteBoards) {
      if (board.mediaId == mediaId) {
        board.setRenderElement(renderEle, params);
        return
      }
    }
    logger.warn("Cannot find white board by mediaId: " + mediaId)
  }

  /**
   * 
   * @param {*} userId 
   * @param {*} mediaId 
   */
  unsetWhiteBoardRender(mediaId) {
    for (let board of this.whiteBoards) {
      if (board.mediaId == mediaId) {
        board.setRenderElement(null, null);
        return
      }
    }
    logger.warn("Cannot find white board by mediaId: " + mediaId)
  }

  /**
   * 
   * @param {*} mediaId 
   * @param {*} mode 
   * @param {*} param 
   */
  @tracelog
  setDisplayMode(mediaId, mode, param) {
    if (mode != DisplayMode.DBWZ 
      && mode != DisplayMode.DBSY 
      && mode != DisplayMode.DBSF) {
      logger.error("Invalid display mode: " + mode);
      return;
    }

    if (mode == DisplayMode.DBSF) { 
      if (!param) {
        logger.error("setDisplayMode param is invalid!");
        return;
      } else {
        if (!Number.isInteger(param)) {
          logger.error("setDisplayMode param should be integer!");
          return;
        }
      }
    }

    if (!mediaId) { // 
      for (const board of this.whiteBoards) {
        board.setDisplayMode(mode, param);
      }
      this.displayMode = mode;
      this.displayParam = param;
    } else { // 
      let board = this._findWhiteBoard(mediaId);
      if (board) {
        board.setDisplayMode(mode, param);
      } else {
        logger.error("Cannot find white board " + mediaId);
      }
    }
  }

  /**
   * 
   * @param {*} params 
   */
  onStartRecvWhiteBoardRes(params) {
    let board = this._findWhiteBoard(params.mediaId)
    if (board) {
      const { renderEle, parentWidth, parentHeight, displayParam, canvasSizeChangeCallback } = board
      const params = {
        parentWidth, 
        parentHeight,
        scaleRatio: displayParam,
        canvasSizeChange: canvasSizeChangeCallback
      }
      renderEle && board.setRenderElement(renderEle, params)
      logger.info("board exits:", board)
    } else {
      let board = new Painter(params);
      this.whiteBoards.push(board);
      if (this.displayMode) { // 
        board.setDisplayMode(this.displayMode, this.displayParam);
      }
      logger.info("Total white board count: " + this.whiteBoards.length)
    }
  }

  /**
   * 
   * @param {*} params 
   */
  @tracelog
  onStopRecvWhiteBoardRes(params) {
    let board = this._findWhiteBoard(params.mediaId)
    if (board) {
      for (let i = 0; i < this.whiteBoards.length; i++) {
        if (this.whiteBoards[i].mediaId == params.mediaId) {
          // this.whiteBoards[i].close();
          this.whiteBoards.splice(i, 1);
          break;
        }
      }
    } else {
      logger.error("Cannot find White baord " + params.mediaId);
    }
  }

  /**
   * 
   */
  @tracelog
  reinitialize() {
    for (const board of this.whiteBoards) {
      board.close();
    }
    this._init();
  }

  /**
   * 
   * @param {*} mediaId 
   */
  removeWhiteBoard(mediaId) {
    this.whiteBoards.forEach(function(board, index, arr) {
      if (board.mediaId == mediaId) {
        arr.splice(index, 1);
        return false;
      }
    })
  }

  /**
   * 
   * @param {*} mediaId 
   */
  getWhiteBoard(mediaId) {
    return this._findWhiteBoard(mediaId)
  }

  /**
   * 
   * @param {*} mediaId 
   */
  _findWhiteBoard(mediaId) {
    for (const board of this.whiteBoards) {
      if (board.mediaId == mediaId) {
        return board;
      }
    }
    return null;
  }

  /**
   * 
   * @param {*} data 
   */
  _onGetWhiteBoardFileUrlRes(data) {
    if (data.code === 0) {
      let filePath = data.data.file_path
      const rootPath = filePath.substr(0, filePath.lastIndexOf('/'))
      let fileUrls = this.cacheFileUrlMap.get(rootPath) || new Map()
      // https
      let httpsUrl = data.data.url
      if (data.data.url.search("https") == -1) {
        httpsUrl = data.data.url.replace(/^http/, "https")
      }
      fileUrls.set(data.data.file_path, httpsUrl)
      this.cacheFileUrlMap.set(rootPath, fileUrls)
    } else {
      console.warn('url')
    }
  }

  _onNotifyGetFileList(data) {
    const fileList = data.data.file_list
    eventRegistry.trigger('onGetFileList', fileList)
  }

  /**
   * 
   * @param {*} file_path
   */
  _getFileUrl (file_path) {
    //  
    if (!file_path) return
    const rootPath = file_path.substr(0, file_path.lastIndexOf('/'))
    if (this.cacheFileUrlMap.get(rootPath) && this.cacheFileUrlMap.get(rootPath).get(file_path)) return
    protocolSender.sendGetWhiteBoardFileUrlReq(file_path)
  }
}

export default new WhiteboardManager();