
import hstWebSocket from '../../util/new_webscoket'
import { MediaType, DisplayMode, Graphics } from "../../../common/enums"
import MsgDispatcher from '../../util/msg-dispatcher'
import { tracelog, colorToNumber, stringToColor, dealCanvasSize } from '../../../common/common'
import logger from '../../../common/logger'
import eventRegistry from '../../../common/event-registry'
import PageStore from "./page-store"
import wbManager from "../white-board-manager"
import Render from './render/render'
import WriteBoard from './write/write'
import protocolSender from "../../util/protocol-sender"
import { promiseHandler } from "../../../common/promise-handler"

const EDIT_WHITE_BOARD = 'edit_white_board'

/**
 * 
 */
class Painter {
  constructor(params) {
    this.isClosing = false
    this.isSelect = false
    this.isRotate = false
    this.isSwitchPage = false
    this.rotateAngle = 0

    // 
    this.pageStore = null;
    this.render = null;

    // 
    this.firstFullData = true;

    // 
    this.scaleRatio = 1;

    // 
    this.width = 0;
    this.height = 0;

    this.write = null;

    // Canvas
    this.parentWidth = 0;
    this.parentHeight = 0;

    // Canvas
    this.canvasWidth = 0;
    this.canvasHeight = 0;


    // Canvas
    this.canvasEle = null;
    this.canvasCtx = null;
    this.canvasSizeChangeCallback = null;

    // CanvasCSS
    this.canvasLeft = 0;
    this.canvasTop = 0;
    this.canvasWidth = 0;
    this.canvasHeight = 0;

    // 
    this.sourcePaints = [];

    this.firstGetFullData = false
    // 
    this.msg_seq_id = null;

    // 
    this.local_seq_id = this.msg_seq_id


    // 
    this.displayMode = DisplayMode.DBSY;
    this.displayParam = null;
    
    // 
    this.userId = params.userId;
    this.mediaId = params.mediaId;
    this.mediaName = params.mediaName;
    this.streamId = params.streamId;
    this.streamToken = params.streamToken;
    this.streamServer = params.streamServer
    this.channelId = ''
    this.seqId = parseInt(Math.random() * (1000 - 3000) + 3000)

    // 
    this.msgDispatcher = new MsgDispatcher();
    this.msgDispatcher.addMsgHandler(30002, this._editRes.bind(this))
    this.msgDispatcher.addMsgHandler(31000, this._dealAmendData.bind(this)) // 
    this.msgDispatcher.addMsgHandler(31002, this._dealAmendData.bind(this)) // 
    this.msgDispatcher.addMsgHandler(30103, this._mouseMove.bind(this)) // 
    this.msgDispatcher.addMsgHandler(31004, this._dealAmendData.bind(this)) //   
    this.msgDispatcher.addMsgHandler(31006, this._dealAmendData.bind(this)) // 
    
    this.msgDispatcher.addMsgHandler(31008, this._dealAmendData.bind(this)) // 
    this.msgDispatcher.addMsgHandler(30200, this._pageChangeHandle.bind(this)) // 
    this.msgDispatcher.addMsgHandler(30202, this._moveGraph.bind(this)) // 
    
    this.msgDispatcher.addMsgHandler(30000, this._heartBeat.bind(this))
    this.msgDispatcher.addMsgHandler(30004, this._dealFullData.bind(this))
    this.msgDispatcher.addMsgHandler(19005, this._processLoginResp.bind(this));

    this.msgDispatcher.addMsgHandler(101, this._onConnectSuccess.bind(this)) //

    // WebSocket
    this.websocket = hstWebSocket
    this.websocket.createProxyConnect({
      backward_addr: this.streamServer,
      handler: this.msgDispatcher,
      seq_id: this.seqId,
      closed: this._onWebsocketClosed.bind(this),
      error: this._onWebsocketError.bind(this)
    });
    return this
  }

  /**
   * 
   * @param {*} msg 
   */
  @tracelog
  _processLoginResp(msg) {
    if (msg.result != 0) {
      logger.warn("Login white board receive channel failed!");
    }
  }

  /**
   * WebSocket
   */
  @tracelog
  _onWebsocketOpen(channelId) {
    protocolSender.sendLoginRecvChannelReq({
      mediaType: MediaType.WHITE_BOARD,
      mediaId: "",
      streamId: this.streamId,
      streamToken: this.streamToken,
      channelId
    })
  }

  /**
   * WebSocekt
   */
  @tracelog
  _onWebsocketClosed() {
    if (!this.isClosing) {
      if (this.websocket.reconnectNum < 10 ) {
        setTimeout(() => {
          this.websocket.reconnect({
              backward_addr: this.streamServer,
              handler: this.msgDispatcher,
              seq_id: this.seqId,
              closed: this._onWebsocketClosed.bind(this),
              error: this._onWebsocketError.bind(this)
            })
        }, 1000)
      } else {
        // throw error  
        eventRegistry.trigger('onError') 
      }
  }
   
    // if (!this.isClosing) {
    //   asyncWrapper(()=>{eventRegistry.trigger("restoreWhiteBoard", {
    //     userId: this.userId,
    //     mediaId: this.mediaId,
    //     parentEle: this.parentEle,
    //     displayMode: this.displayMode,
    //     displayParam: this.displayParam})
    //   })
    //   this.close();
    // }
  }

  /**
   * WebSocket
   */
  @tracelog
  _onWebsocketError() {
  }

  _onConnectSuccess(msg, socket) {
    console.warn('_onConnectSuccess msg', msg)
    const {result, channel_id, seq} = msg
    if (result === 0) {
      if (seq === this.seqId) {
        this.channelId = channel_id
        this._onWebsocketOpen(channel_id)
      } else {
        logger.info('proxyChannel has exist')
      }
    }
  }

  switchBoard() {
    // canvasCtxcanvas
    this.canvasCtx.canvas.width = this.canvasWidth
    this.canvasCtx.canvas.height = this.canvasHeight
    this.canvasSizeChangeCallback && this.canvasSizeChangeCallback(
      this.canvasLeft,
      this.canvasTop,
      this.canvasWidth, 
      this.canvasHeight
    )
  }

  /**
   * 
   * @param {*} mode 
   * @param {*} param 
   */
  setDisplayMode(mode, param) {
    if (this.displayMode != mode // 
      || (mode == DisplayMode.DBSF && param && this.displayParam != param)) { // 
      this.displayMode = mode;
      this.displayParam = param;
      this._showWhiteBoard();
    }
  }

  /**
   * 
   */
  _showWhiteBoardDbwz() {
    // 
    let showRatio = this.parentHeight / this.parentWidth.toFixed(6);
    let dataRatio =  this.height / this.width.toFixed(6);

    // 
    if (showRatio > dataRatio ) {
      // Canvas
      this.canvasWidth = this.parentWidth;
      this.canvasHeight = this.height * this.parentWidth / this.width.toFixed(6);

      // Canvas
      this.canvasLeft = 0;
      this.canvasTop = (this.parentHeight - this.canvasHeight) / 2
    } else { // 
      // Canvas
      this.canvasWidth = this.width * this.parentHeight / this.height.toFixed(6);
      this.canvasHeight = this.parentHeight;

      // Canvas
      this.canvasLeft = (this.parentWidth - this.canvasWidth) / 2;
      this.canvasTop = 0;
    }

    // Canvas
    this.canvasSizeChangeCallback(
      this.canvasLeft,
      this.canvasTop,
      this.canvasWidth, 
      this.canvasHeight)

    // 
    this.scaleRatio = this.canvasHeight / this.height.toFixed(6);
    this.canvasEle.width = this.canvasWidth
    this.canvasEle.height = this.canvasHeight

    this._refreshRender();
  }

  /**
   * 
   */
  _showWhiteBoardDbsy() {
    // 
    let showRatio = this.parentHeight / Number(this.parentWidth).toFixed(6);
    let dataRatio =  this.height / Number(this.width).toFixed(6);

    // 
    if (showRatio > dataRatio ) {
      // Canvas
      this.canvasWidth = this.width * this.parentHeight / Number(this.height).toFixed(6);
      this.canvasHeight = this.parentHeight;

      // Canvas
      this.canvasLeft = 0;
      this.canvasTop = 0;
    } else { // 
      // Canvas
      this.canvasWidth = this.parentWidth;
      this.canvasHeight = this.height * this.parentWidth / Number(this.width).toFixed(6);

      // Canvas
      this.canvasLeft = 0;
      this.canvasTop = 0;
    }

    const { width, height } = dealCanvasSize(this.canvasWidth, this.canvasHeight)
    this.canvasWidth = Number(width).toFixed(6)
    this.canvasHeight = Number(height).toFixed(6)

    // Canvas
    this.canvasSizeChangeCallback(
      this.canvasLeft,
      this.canvasTop,
      this.canvasWidth, 
      this.canvasHeight)

    // 
    this.scaleRatio = this.canvasHeight / this.height.toFixed(6);

    // Canvas
    this.canvasEle.width = this.canvasWidth
    this.canvasEle.height = this.canvasHeight

    this._refreshRender();
  }

  /**
   * 
   */
  _showWhiteBoardDbsf() {
     // Canvas
     this.canvasWidth = this.width * this.displayParam.toFixed(6) / 100;
     this.canvasHeight = this.height * this.displayParam.toFixed(6) / 100;
 
     // Canvas
     if (this.canvasWidth >= this.parentWidth) {
       this.canvasLeft = 0;
       // this.parentEle.style.overflowX = 'scroll';
     } else {
       this.canvasLeft = (this.parentWidth - this.canvasWidth) / 2;
       // this.parentEle.style.overflowX = 'hidden';
     }
     
     if (this.canvasHeight >= this.parentHeight) {
       this.canvasTop = 0;
       // this.parentEle.style.overflowY = 'scroll';
     } else {
       this.canvasTop = (this.parentHeight - this.canvasHeight) / 2;
       // this.parentEle.style.overflowY = 'hidden';
     }
 
     // Canvas
     this.canvasSizeChangeCallback(
       this.canvasLeft,
       this.canvasTop,
       this.canvasWidth, 
       this.canvasHeight)
 
     // 
     this.scaleRatio = this.displayParam.toFixed(6) / 100;
 
     // Canvas
     this.canvasEle.width = this.canvasWidth
     this.canvasEle.height = this.canvasHeight

    this._refreshRender();
  }

  /**
   * 
   */
  _showWhiteBoard() {
    if (!this.canvasEle || !this.canvasCtx) {
      console.warn("Invalid canvas render!")
      return;
    }

    // 
    if (this.pageStore && this.pageStore.getConvertFilePath()) {
      this.render && this.render._insertImage()
    }

    // 
    if (this.displayMode == DisplayMode.DBWZ) {
      this._showWhiteBoardDbwz();
    } else if (this.displayMode == DisplayMode.DBSY) {
      this._showWhiteBoardDbsy();
    } else if (this.displayMode == DisplayMode.DBSF) {
      this._showWhiteBoardDbsf();
    } else {
      logger.error("Unknown display mode: " + this.displayMode);
    }
  }

  /**
 * Canvas
 * @param {*} parentEle 
 */
  @tracelog
  setRenderElement(renderEle, params) {
    logger.warn("Set render element: " + JSON.stringify(params));
    logger.warn("renderEle", [renderEle]);
    if (renderEle) {
      this.canvasEle = renderEle;

      // this.canvasEle.width = this.width
      // this.canvasEle.height = this.height

      this.parentWidth = params.parentWidth;
      this.parentHeight = params.parentHeight;

      if (!params.parentWidth || !params.parentWidth) {
        const { screenWidth, screenHeigh } = wx.getSystemInfoSync()
        this.parentWidth = screenWidth
        this.parentHeight = screenHeigh
      }

      this.displayParam = params.scaleRatio || 1
      this.canvasSizeChangeCallback = params.canvasSizeChange;

      // Canvas
      this.canvasCtx = this.canvasEle.getContext('2d');

      if (this.pageStore) {
        this.pageStore = new PageStore(this.sourcePaints)
        this.render = new Render(this, this.pageStore)
        this.write = new WriteBoard(this, this.pageStore)
      }

      // 
      this._showWhiteBoard();
    } else {
      // 
      this._emptyCanvas();

      // Canvas
      if (this.canvasEle) {
        this.canvasEle.width = 0
        this.canvasEle.height = 0
        this.canvasCtx = null;
        this.canvasEle = null;
      }
    
      this.canvasSizeChangeCallback = null;

      this.parentWidth = 0;
      this.parentHeight = 0;
      throw new Error('can not find canvasEle!!')
    }
  }

  /**
   * canvas
   * @param {*} boardInfo 
   */
  _initCanvas (boardInfo) {
    // 
    this.width = boardInfo.width
    this.height = boardInfo.height

    // 
    if (this.canvasCtx) {
      this.canvasCtx.clearRect(0, 0, boardInfo.width, boardInfo.height);
    }

    // URL
    // wbManager._getFileUrl(boardInfo.convert_file_path, boardInfo.page)
    
    // 
    if (this.pageStore && this.pageStore.getConvertFilePath()) {
      this.render && this.render._insertImage()
    }
    // 
    this._refreshRender()
  }

  /**
   * URL
   */
   _waitGetImageUrl() {
    const convertPath = this.pageStore.getConvertFilePath()
    return new Promise(function(resolve, reject) {
      let count = 0
      let handle = setInterval(() => {
        ++count
        if (wbManager.cacheFileUrlMap.get(convertPath)) {
          clearInterval(handle)
          resolve()
        } else {
          if (count > 10) { // 5
            clearInterval(handle)
            reject("URL")
          }
        }
      }, 500)
    })
  }

  /**
   * 
   */
  _drawImage() {
    let self = this
    const convertPath = this.pageStore.getConvertFilePath()
    let rootPath = convertPath
    const curPage = this.pageStore.getCurPage()
    let fullPath = convertPath
    if (!String(fullPath).toLowerCase().includes('.jpg')) {
      fullPath = `${convertPath}/${curPage}.jpg`
    } else {
      rootPath = convertPath.substr(0, convertPath.lastIndexOf('/'))
    }

    return new Promise(function(resolve, reject) {
      let imgUrl = wbManager.cacheFileUrlMap.get(rootPath).get(fullPath)
      logger.info('imgUrl')
      logger.info(imgUrl)
      wx.downloadFile({
        url: imgUrl,
        timeout: 5000,
        success: (res)=> {
          logger.info('downloadFile res')
          logger.info(JSON.stringify(res))
          if (res.statusCode === 200 && self.canvasEle) {
            let seal = self.canvasEle.createImage();
            seal.src = res.tempFilePath
            seal.onload = function() {
              try {
                logger.info("")
                if (self.canvasCtx) {
                  self.canvasCtx.drawImage(
                    seal, 
                    0,
                    0,
                    self.canvasWidth, 
                    self.canvasHeight);
                }
                resolve();
              } catch(err) {
                logger.error('draw image error', err);
              }
            }
            seal.onerror = function(res) {
              logger.error("", res)
              reject()
            }
          }
        },
        fail: function(err) {
          logger.warn("download image failed!")
          logger.warn(err)
          reject()
        }
      })
    })
  }

  /**
   * render
   */
   _refreshRender() {
    let self = this
    self._emptyCanvas()
    if (self.pageStore && self.pageStore.getConvertFilePath()) {
      self._waitGetImageUrl().then(()=>{
        logger.log("Image url is ready.")
        self._drawImage().then(()=>{
          logger.log("Draw image finished.")
          this.render && this.render._drawAllPaint()
        }).catch(()=>{
          logger.log("Draw image failed!")
        })
      }).catch((err)=>{
        logger.warn(err)
      })
    } else {
      logger.log("")
      this.render && this.render._drawAllPaint()
    }
  }

  /**
   * 
   */
  _emptyCanvas() {
    if (this.canvasCtx) {
      this.canvasCtx.clearRect(0, 0, this.canvasWidth, this.canvasHeight);
    }
  }

  /**
 * 
 */
  _drawAllPaint() {
    if (this.pageStore) {
      let paintObjs = this.pageStore.findPagePaints(this.pageStore.getCurPage())
      paintObjs.map((item) => {
        this._drawPaint(item.type, item[item.type])
      })
    }
  }

  /**
   * 
   * @param {*} pageData 
   * @param {*} paintObj 
   */
  _drawAmendData (pageData, paintObj) {
    if (paintObj.type !== Graphics.CURVE) {
      this._emptyCanvas()
      this._drawAllPaint()
    } else { // 
      const paint = this.pageStore.findPaintByGid(pageData, paintObj[paintObj.type].gid)
      if (paint) {
        this._drawPaint(paint.type, paint[paint.type])
      }
    }
  }

  /**
   * // 
   * @param {*} paintObj 
   */
  _dealAmendData (paintObj) {
    if (paintObj.channel_id !== this.channelId) return
    this._updateMsgSeq(paintObj);
    this.render && this.render.dealAmendData(paintObj)
  }

  /**
   * 
   * @param {*} pageObj 
   */
  _pageChangeHandle (pageObj) {
    if (pageObj.channel_id !== this.channelId) return
    this._updateMsgSeq(pageObj);
    this.render && this.render.pageChange(pageObj)
  }

  /**
   * 
   * @param {*} paintObj 
   */
  _moveGraph (paintObj) {
    this._updateMsgSeq(paintObj);
    this.render && this.render.moveGraph(paintObj)
  }
  /**
   * 
   * @param {*} paintObj 
   */
  _mouseMove (paintObj) {
    this._updateMsgSeq(paintObj)
    const { rotate_angle, cur_page } = paintObj
    if (rotate_angle || rotate_angle === 0) {
      this.rotateAngle = rotate_angle
      eventRegistry.trigger('shareOperation', {
        userId: this.userId,
        mediaId: this.mediaId, 
        rotateAngle: rotate_angle,
        type: 'rotate'
      })
    }
    if (cur_page || cur_page === 0) {
      this._pageChangeHandle(paintObj)
    }
  }

  // 
  _editRes (data) {
    if (data.channel_id !== this.channelId) return
    this.pageStore.editConfig.id = data.edit_id
    if (this.isSelect) {
      protocolSender.sendSwitchWhiteBoard(data.edit_id, this.channelId)
      this.isSelect = false
    } else if (this.isRotate) {
      const params = {
        edit_id: data.edit_id,
        rotateAngle: this.rotateAngle,
      }
      protocolSender.sendChangeWhiteBoardProperty(params, this.channelId)
      this.isRotate = false
    } else if (this.isSwitchPage) {
      const params = {
        edit_id: this.pageStore.editConfig.id,
        cur_page: this.pageStore.getCurPage(),
      }
      protocolSender.sendChangeWhiteBoardProperty(params, this.channelId)
      this.isSwitchPage = false
    } else {
      this.pageStore.editConfig.status = true
      eventRegistry.trigger(EDIT_WHITE_BOARD, 0, true)
    }
  }

  // select() {
  //   if (this.pageStore && this.pageStore.editConfig && this.pageStore.editConfig.id) {
  //     protocolSender.sendSwitchWhiteBoard(this.pageStore.editConfig.id, this.channelId)
  //   } else {
  //     this.isSelect = true
  //     protocolSender.sendOpenEdit(this.channelId)
  //   }
  // }

  rotate(rotateAngle) {
    this.rotateAngle = rotateAngle
    if (this.pageStore.editConfig.id) {
      const params = {
        edit_id: this.pageStore.editConfig.id,
        rotateAngle,
      }
      protocolSender.sendChangeWhiteBoardProperty(params, this.channelId)
    } else {
      this.isRotate = true
      protocolSender.sendOpenEdit(this.channelId)
    }
  }

  switchBoardPage(curPage, needNotifyOthers) {
    this.pageStore.setCurPage(curPage)
    if (needNotifyOthers) {
      if (this.pageStore.editConfig.id) {
        const params = {
          edit_id: this.pageStore.editConfig.id,
          cur_page: curPage,
        }
        protocolSender.sendChangeWhiteBoardProperty(params, this.channelId)
      } else {
        this.isSwitchPage = true
        protocolSender.sendOpenEdit(this.channelId)
      }
    } else {
      if (this.pageStore && this.pageStore.getConvertFilePath()) {
        this.render && this.render._insertImage()
      }
      this._refreshRender()
    }
  }

  /**
   * 
   */
  @tracelog
  close() {
    logger.log("Close white board: " + this.mediaId)
    this.isClosing = true;

    this.pageStore = null;
    this.msgDispatcher = null;

    if (this.intervalHandle) {
      clearInterval(this.intervalHandle);
      this.intervalHandle = null;
    }

    if (this.parentEle && this.canvasEle) {
      this.parentEle.removeChild(this.canvasEle);
      //this.parentEle = null;
      this.canvasEle = null;
    }

    if (this.websocket) {
      this.websocket.close();
      this.websocket = null;
    }
    // let params = {
    //   userId: null,
    //   mediaType: MediaType.WHITE_BOARD,
    //   mediaId: this.mediaId,
    //   mediaName: this.pageStore.getBoardName()
    // };
    // eventRegistry.trigger("onUnPublishMedia", params);
  }
  
  /**
   * 
   * @param {*} msg 
   */
  _dealFullData(msg) {
    if (msg.channel_id !== this.channelId) return
    // 
    this.sourcePaints = this.sourcePaints.concat(msg.data)
    // 
    if (msg.sync_end) {
      logger.log("Get white board full data complete.");
      this.pageStore = new PageStore(this.sourcePaints)
      this.render = new Render(this, this.pageStore)
      this.write = new WriteBoard(this, this.pageStore)
      if (this.sourcePaints[0].board) {
        this._initCanvas(this.sourcePaints[0].board)
      } else {
        logger.error("Invalid source paints: " + JSON.stringify(this.sourcePaints));
        this.sourcePaints = [];
        return;
      }

      // 
      if (this.firstFullData) {
        if (!wbManager.tryRestoreWhiteBoardRender(this.mediaId)) {
          const board = this.sourcePaints[0].board
          const rotateAngle = board.property && board.property.rotate_angle || 0
          const { rights_index = 0, name, ex_name } = JSON.parse(board.user_data)
          let boardName = name
          if (ex_name && !String(boardName).includes(String(ex_name))) {
            boardName = `${name}.${ex_name}`
          }
          eventRegistry.trigger("onRemoteMediaAdd", { 
            userId: this.userId, 
            mediaType: MediaType.WHITE_BOARD, 
            mediaId: this.mediaId,
            rotateAngle,
            rightIndex: rights_index,
            mediaName: boardName || this.pageStore.getBoardName(),
            totalPage: this.pageStore.getPageCount(),
            curPage: this.pageStore.getCurPage(),
          })
        }
        this.firstFullData = false;
      }

      this.sourcePaints = [];
    }
  }
  _updateMsgSeq (msg) {
    const { channel_id, seq_id } = msg
    if (channel_id === this.channelId) {
      this.msg_seq_id = seq_id
    }
  }

  /**
   * 
   * @param {*} msg 
   */
  _checkMsgSeq(msg) {
    const { seq_id } = msg
    if (!this.firstGetFullData || !this.msg_seq_id // 
      || (this.msg_seq_id !== null && seq_id - this.msg_seq_id != 1)) { // 
      protocolSender.sendGetWhiteBoardFullDataReq(this.channelId);
      this.firstGetFullData = true
    }
    this.msg_seq_id = seq_id
    this.local_seq_id = this.msg_seq_id
  }

  /**
   * 
   * @param {*} msg 
   */
  _heartBeat (msg) {
    if (msg.channel_id === this.channelId) {
      this._checkMsgSeq(msg);
    }
  }
    /**
   * 
   * @param {*} type  
   * @param {*} params  
   * @param {*} action 1  2   3  5 
   */
  _sendGraphMsg (type, params, action) {
    let msg = {
      type: type,
      seq_id: ++this.local_seq_id,
      edit_id: this.pageStore.editConfig.id,
      channelId: this.channelId
    }
    params.action = action
    params.line_shape = 0
    msg[type] = params
    if (type === Graphics.LINE) {
      msg.id = 31000
    } else if(type === Graphics.CURVE || type === Graphics.MASK_CURVE) {
      msg.id = 31002
    } else if (type === Graphics.TEXT) {
      msg.id = 31006
    } else if (type === Graphics.GMOV) {
      delete params.action
      msg.id = 30202
    }
    protocolSender.sendGraphMsg(msg)
  }
  /**
   * 
   */
  clear () {
    let params = {
      curPage: this.pageStore.getCurPage(),
      edit_id: this.pageStore.editConfig.id,
      // seq_id: this.msg_seq_id + 1,
      action: 3,
      channelId: this.channelId
    }
    protocolSender.sendPageInfoMsg(params)
    this.pageStore.revokeStack = []
    this.pageStore.restoreStack = []
  }
  // 
  brushSize (size) {
    //  
    if (size === undefined) {
      return this.pageStore.brush_size
    }
    this.pageStore.brush_size = parseInt(size) || this.pageStore.brush_size
  }
  // 
  brushColor (color) {
    if (color === undefined) {
      return stringToColor(this.pageStore.color)
    }
    this.pageStore.color = colorToNumber(color) || this.pageStore.color
  }
  // 
  toolType (shape) {
    if (shape === undefined) {
      return this.pageStore.shape
    }
    this.pageStore.shape = shape || this.pageStore.shape
  }
  _transrate (stack1, stack2) {
    let paint = stack1.pop();
    if(!paint) return
    let graphType = paint.type
    let pageData = this.pageStore.findPagePaints(paint[graphType].pid);
    let movePaint = this.pageStore.findPaintByGid(pageData, paint[graphType].gid)
    //   
    paint = movePaint || paint
    if (paint[graphType].action === 1 || paint[graphType].action === 2) {
      this._sendGraphMsg(graphType, paint[graphType], 3)
      stack2.push(paint);
    } else if (paint[graphType].action === 3) {
      this._sendGraphMsg(graphType, paint[graphType], 1)
      stack2.push(paint);
    }
  }
  // 
  revoke () {
    this._transrate(this.pageStore.revokeStack, this.pageStore.restoreStack)
  }
  // 
  restore () {
    this._transrate(this.pageStore.restoreStack, this.pageStore.revokeStack)
  }
   /**
   * 
   */
  edit (isEdit) {
    if (isEdit === undefined) {
      return this.pageStore.editConfig.status
    } else {
      if(isEdit) {
        protocolSender.sendOpenEdit(this.channelId)
        return promiseHandler(EDIT_WHITE_BOARD)
      } else {
        this.pageStore.editConfig.status = false
        return Promise.resolve(false)
      }
    }
  }
  page (num) {
    if (num === undefined) {
      return this.pageStore.getCurPage()
    }
    // 
    if(num >= this.pageStore.getPageCount() || num < 1 ) return logger.warn('page number is not reasonable')
    let params = {
      curPage: num,
      edit_id: this.pageStore.editConfig.id,
      // seq_id: this.msg_seq_id + 1,
      action: 0,
      channelId: this.channelId
    }
    protocolSender.sendPageInfoMsg(params)
  }

  startPainting(event) {
    eventRegistry.trigger(`touchStart${this.mediaId}`, event)
  }

  painting(event) {
    eventRegistry.trigger(`touchMove${this.mediaId}`, event)
  }

  stopPainting(event) {
    eventRegistry.trigger(`touchEnd${this.mediaId}`, event)
  }
}

export default Painter