import Store from "../util/store"
import protocolSender from "../util/protocol-sender"

class UploadManager {
  constructor () {
    Store.msgDispatcher.addMsgHandler(20011, this._onRecvOpenUploadMsg.bind(this));
    Store.msgDispatcher.addMsgHandler(20003, this._onRecvCloseUploadMsg.bind(this));
    Store.msgDispatcher.addMsgHandler(21001, this._onRecvTransCodeMsg.bind(this));
  }
  startUpload (params) {
    this._init(params)
    this.startUpload_hook.call(this)
    protocolSender.sendOpenUpload({
      file_path: `/${this.file_path}/0.jpg`,
      size: this.size
    })
  }

  _init(params) {
    // 
    this.startUpload_hook = params.onStartUpload || function () {}
    // jpg
    this.uploadSuccess_hook = params.onUploadSuccess || function () {}
    // 
    this.startTransCode_hook = params.onStartTransCode || function () {}
    // 
    this.transCodeSuccess_hook = params.onTransCodeSuccess || function () {}

    this.failed_hook = params.onFiled || function () {}

    this.fileTempPath = params.file
    this.file_name = params.file_name
    this.size = params.file_size
    this.upload_mode = params.upload_mode || 0
    this.check_code = params.CheckCode
    this.appid = params.SrvAppID
    this.link = params.SrvAddrLink
    this.user_id = params.UserID
    this.convert_file_path = String(params.Guid).toUpperCase()
    this.file_path = String(params.Guid).toUpperCase()
    this.upLoadControl = false

  }
  /**
   * 
   * @param {*} data 
   */
  _onRecvOpenUploadMsg (data) {
    if (data.code === 0) {
      if (this.upLoadControl) return
      const { bucket, end_point, access_id, signature, policy, key } = data.data
      const guid = this.file_path
      const url = `https://${bucket}.${end_point}`
      this.upLoadControl = true
      setTimeout(()=> {
        this.upLoadControl = false
      }, 1000)
      wx.uploadFile({
        url: url,
        filePath: this.fileTempPath,
        name: 'file',
        header: {
          'content-type': `multipart/form-data; boundary=${guid}`,
        },
        formData: {
          'AccessKeyID': access_id,
          'signature': signature,
          'policy': policy,
          'key': key,
        },

        success: (res)=> {
          console.log(res.statusCode);            //
          if(res.statusCode == '204'){
            console.warn('', res)
            wx.showToast({
              title: '',
              icon: ''
            });
            const params = {
              guid,
              check_code: this.check_code,
              appid: this.appid,
              link: this.link,
              file_path: `/${this.file_path}/0.jpg`,
              file_name: this.file_name,
              file_size: this.size,
              user_id: this.user_id
            }
            protocolSender.sendUploadEnd(params)
          }
          else{
            console.log('', res)
            wx.showToast({
              title: '',
              icon: ''
            });
          }
        },
        fail: function(e){
          console.log(e);
        }
      })
    } else {
      this.failed_hook.call(this, {
        code: -1,
        msg: data.msg
      })
    }
  }
  /**
   * 
   */
  _onRecvCloseUploadMsg (data) {
    if (data.code === 0) {
      if (this.file_name.includes('.jpg')) {
        this.uploadSuccess_hook.call(this, {
          board_name: this.file_name,
          convert_file_path: data.data.file_path,
          file_path: data.data.file_path,
          check_code: this.check_code,
          appid: this.appid,
          link: this.link,
          id: this.convert_file_path
        })
      } else {
        this.startTransCode_hook.call(this)
        protocolSender.sendTransCodeMsg(this.file_path)
      }
    } else {
      this.failed_hook({
        code: -1,
        msg: data.msg
      })
    }
  }
  /**
   * 
   */
  _onRecvTransCodeMsg (data) {
    if (data.code === 0) {
      data = data.data
      this.transCodeSuccess_hook.call(this, {
        board_name: this.file_name,
        convert_file_path: data.convert_file_path,
        file_path: data.file_path,
        width: data.width,
        height: data.height,
        page: data.convert_counts
      })
    } else {
      this.failed_hook.call(this, {
        code: -1,
        msg: data.msg
      })
    }
  }
}


