
import StateMachine from 'javascript-state-machine'
import Store from './util/store'
import { asyncWrapper, getAddrs } from '../common/common'
import hstWebSocket from './util/new_webscoket'
import { EventResult, EventType } from '../common/enums'
import protocolSender from './util/protocol-sender'
import logger from "../common/logger"
import boardManager from "./media/white-board-manager"
import eventRegistry from '../common/event-registry'
import txRoom from "./util/tx-room"

/**
 * 
 */
let RtcEngine = new StateMachine({
  init: 'uninit',
  transitions: [
    {name: 'doInit',        from: ['uninit', 'restoring'],    to: 'initing'},   // 
    {name: 'initSuccess',   from: 'initing',   to: 'inited'},    // 
    {name: 'initFailed',    from: 'initing',   to: 'uninit'},    // 
    {name: 'doLogin',       from: 'inited',    to: 'logining'},  // 
    {name: 'loginSuccess',  from: 'logining',  to: 'logined'},   // 
    {name: 'loginFailed',   from: 'logining',  to: 'inited'},    // 
    {name: 'doJoin',        from: 'logined',   to: 'joining'},   // 
    {name: 'joinSuccess',   from: 'joining',   to: 'joined'},    // 
    {name: 'joinFailed',    from: 'joining',   to: 'logined'},   // 
    {name: 'doLeave',       from: 'joined',    to: 'leaving'},   // 
    {name: 'leaveSuccess',  from: 'leaving',   to: 'logined'},   // 
    {name: 'leaveFailed',   from: 'leaving',   to: 'joined'},    // 
    {name: 'doLogout',      from: 'logined',   to: 'logouting'}, // 
    {name: 'logoutSuccess', from: 'logouting', to: 'inited'},    // 
    {name: 'logoutFailed',  from: 'logouting', to: 'logined'},   // 
    {name: 'doDestroy',     from: ['logined', 'joined','inited', 'logining', 'joining', 'restoring', 'uninit', 'initing'], to: 'uninit'},    // 
    {name: 'doRestore',     from: 'joined',    to: 'restoring'}, //  
  ],
  data: {
    registered: false,
    forceLogouted: false,
    kickedOut: false,
    hasLogin: false
  },
  methods: {
    /**
     * websocket
     */
    onWebSocketClosed() {
      logger.warn("Signalling websocket closed, state: " + this.state)
      if (Store.websocket) {
        Store.websocket.close()
        Store.websocket = null
      }

      // TODO: 
      if (this.state == 'joined' && !this.forceLogouted && !this.kickedOut) {
        this.doRestore();
      }
    },

    /**
     * 
     */
    onLeaveGroup() {
      Store.userInfo.groupId = null;
      boardManager.reinitialize();
    },

    /**
     * 
     */
    onLogout() {
      this.onLeaveGroup();
      Store.userInfo.userId = null;
    },

    /**
     * 
     */
    onDestroy() {
      this.onLogout();

      this.hasLogin = false

      Store.userInfo.appId = null;
      Store.userInfo.token = null;
      Store.userInfo.companyId = null;
      
      if (Store.websocket) {
        Store.websocket.close();
        Store.websocket = null;
      }
    },

    //--------------------------------------------------------------------------
    // 
    //--------------------------------------------------------------------------
    
    /**
     * 
     * @params {*} msg 
     * @params {*} socket 
     */
    onLoginRes(msg, socket) {
      let self = this;
      if (msg.result == 0) {
        txRoom.getLoginInfo(Store.userInfo.userId, 
          function(res) {
            self.loginSuccess({
              sdkAppId: res.data.sdkAppID,
              userSig: res.data.userSig,
              accountType: res.data.accountType})
          }, 
          function(res) {
            self.loginFailed();
          }
        );
      } else {
        self.loginFailed(msg.result)
      }
    },

    /**
     * 
     * @params {*} msg 
     * @params {*} socket 
     */
    onLogoutRes(msg, socket) {
      (msg.result == 0) ? this.logoutSuccess() : this.logoutFailed();
    },

    /**
     * 
     * @params {*} msg 
     * @params {*} socket 
     */
    onJoinGroupRes(msg, socket) {
      let self = this;
      if (msg.result == 0) {
        let userId = Store.userInfo.userId
        txRoom.isPass && (userId = msg.room_id + "_" + userId)
        const success = (res, sdkAppId, userSig)=> {
          console.log("createRoom success." + JSON.stringify(res));
          self.joinSuccess({
            // RoomIDJavaRoomIDRoomID
            // RoomIDJavaRoomID
            serverRoomId: res.data.roomID,
            serverUserId:userId,
            txRoomId: msg.room_id,
            privateMapKey: res.data.privateMapKey,
            sdkAppId: sdkAppId,
            userSig: userSig
          })
        }

        const fail = ()=> {
          console.error("Creat room failed!");
          self.joinFailed();
        }

        txRoom.getLoginInfo(
          userId,
          function(res) {
            let sdkAppId = res.data.sdkAppID;
            let userSig = res.data.userSig;
            if (txRoom.isPass) {
              txRoom.createRoom(
                Store.userInfo.userId, 
                Store.userInfo.groupId,
                success(res, sdkAppId, userSig),
                fail
              );
            } else {
              txRoom.enterRoom(
                Store.userInfo.userId, 
                msg.room_id,
                success(res, sdkAppId, userSig),
                fail
              );
            }
          },
          function(res) {
            console.error("Get login info failed!");
            self.joinFailed();
          }
        );
      } else {
        this.joinFailed();
      }
    },

    /**
     * 
     * @params {*} msg 
     * @params {*} socket 
     */
    onLeaveGroupRes(msg, socket) {
      (msg.result == 0) ? this.leaveSuccess() : this.leaveFailed();
    },

    /**
     * 
     * @params {*} msg 
     * @params {*} socket 
     */
    onForceLogout(msg, socket) {
      this.forceLogouted = true
      this.onLogout()
      eventRegistry.trigger("onUserForceLogout");
    },

    /**
     * 
     * @params {*} msg 
     * @params {*} socket 
     */
    onKickOut(msg, socket) {
      this.kickedOut = true
      this.onLogOut()
      protocolSender.sendKickOutRes(); // 
      eventRegistry.trigger("onKickOut");
    },

    _onConnectSuccess(msg, socket) {
      if (msg.result == 0) {
        // webScoket
        if (this.hasLogin) return
        Store.mainWebsocketChannelId = msg.channel_id
        this.hasLogin = true
        let options = {
          appId: Store.userInfo.appId,
          userId: Store.userInfo.userId,
          token: Store.loginParams.token,
          companyId: Store.loginParams.companyId,
          osType: "webchat",
          mutexType: Store.loginParams.mutexType,
          forceLogin: Store.loginParams.forceLogin,
          registered: Store.loginParams.registered,
          extendInfo: Store.loginParams.extendInfo,
        }
        protocolSender.sendLoginReq(options)
      }
    },
    
    //--------------------------------------------------------------------------
    // 
    //--------------------------------------------------------------------------

    /**
     * 
     * @params {*} lifecycle 
     * @params {*} params 
     */
    onDoInit: function(lifecycle, params) {
      const { txRoomUrl, appId, isPass = false } = params
      txRoom.isPass = isPass
      if (!txRoomUrl || !appId) {
        this.initFailed()
        return
      }
      txRoom.serverDomain = txRoomUrl
      txRoom.appId = appId
      if (!this.registered) {
        Store.msgDispatcher.addMsgHandler(10001, this.onLoginRes.bind(this));
        Store.msgDispatcher.addMsgHandler(10003, this.onLogoutRes.bind(this));
        Store.msgDispatcher.addMsgHandler(10004, this.onForceLogout.bind(this));
        Store.msgDispatcher.addMsgHandler(12007, this.onKickOut.bind(this));
        Store.msgDispatcher.addMsgHandler(14101, this.onJoinGroupRes.bind(this));
        Store.msgDispatcher.addMsgHandler(14103, this.onLeaveGroupRes.bind(this));
        Store.msgDispatcher.addMsgHandler(101, this._onConnectSuccess.bind(this));
        this.registered = true;
      }

      asyncWrapper(()=>{this.initSuccess()});
    },
    /**
     * 
     * @param {*} lifecycle 
     */
    onTransition(lifecycle) {
      logger.state("transition: " + lifecycle.transition + ", from " + lifecycle.from + " to " + lifecycle.to)
    },

    /**
     * 
     * @params {*} lifecycle 
     * @params {*} params 
     */
    onInitSuccess: function(lifecycle, params) {
      if (!Store.restoring) {
        eventRegistry.trigger(EventType.EVENT_INIT, EventResult.SUCCESS)
      } else {
        let params = {
          userId: Store.userInfo.userId,
          appId: Store.userInfo.appId,
          companyId: Store.loginParams.companyId,
          token: Store.loginParams.token,
          forceLogin: true, // 
          mutexType: Store.loginParams.mutexType,
          extendInfo: Store.loginParams.extendInfo,
          accessUrl: Store.loginParams.accessUrl
        }
        asyncWrapper(()=>{this.doLogin(params)})
      }
    },

    /**
     * 
     * @params {*} lifecycle 
     * @params {*} params 
     */
    onInitFailed: function(lifecycle, params) {
      if (!Store.restoring) {
        eventRegistry.trigger(EventType.EVENT_INIT, EventResult.FAILED);
      } else {
        Store.restoring = false
        logger.warn("Restoring failed for init!")
      }
    },
    
    /**
     * 
     * @params {*} lifecycle 
     * @params {*} params 
     */
    onDoLogin: function(lifecycle, params) {
      console.log('paas login params', params)
      if (!params.userId) {
        asyncWrapper(()=>{this.loginFailed("Invalid userId!")})
      } else {
        Store.userInfo.userId = params.userId;
      }

      if (!params.appId) {
        asyncWrapper(()=>{this.loginFailed("Invalid appId!")})
      } else {
        Store.userInfo.appId = params.appId;
      }

      if (!params.token) {
        asyncWrapper(()=>{this.loginFailed("Invalid token!")})
      } else {
        Store.loginParams.token = params.token;
      }

      // Access
      if (!params.accessUrl) {
        params.accessUrl = "https://access.paas.hst.com/server/address";
      }
      Store.loginParams.accessUrl = params.accessUrl

      if (params.forceLogin) {
        Store.loginParams.forceLogin = params.forceLogin ? 1 : 0;
      }

      if (params.extendInfo) {
        Store.loginParams.extendInfo = params.extendInfo;
      }

      if (params.companyId) {
        Store.loginParams.companyId = params.companyId;
      }

      if (params.mutexType) {
        Store.loginParams.mutexType = params.mutexType;
      }

      let self = this;
      try {
        // WebSocket
        wx.request({
          url: Store.buildGetProxyWayUrl(params.accessUrl),
          data: {},
          method: 'GET',
          header: {
            'content-type': 'application/json' // 
          },
          success: function (res) {
            console.log("Response from access service: " + JSON.stringify(res));
            if (res.data.code == 0) {
              const addrUrl = res.data.result
              wx.request({
                url: Store.buildGetGateWayUrl(params.accessUrl),
                data: {},
                method: 'GET',
                header: {
                  'content-type': 'application/json' // 
                },
                success: function (res) {
                  if (res.data.code == 0) {
                    const backwardAddr = res.data.result;
                    if (backwardAddr) {
                      let options = {
                        addrUrl,
                        // addrUrl: 'wss://paas-demo-private.haoshitong.com:21000/wsproxy1',
                        backward_addr: backwardAddr,
                        handler: Store.msgDispatcher,
                        seq_id: parseInt(Math.random() * (1 - 1000) + 1000),
                        closed() { self.onWebSocketClosed(); },
                        error() { self.loginFailed("Websocket error!"); }
                      };
                      Store.websocket = hstWebSocket
                      Store.websocket.createProxyConnect(options)
                    }
                  } else {
                    throw "Cannot find websocket gateway from response!";
                  }
                },
                fail: function (res) {
                  throw "Get websocket gateway failed";
                },
                complete: function (res) {
                  console.log("Get websocket gateway complete");
                }
              })
            } else {
              throw "Get websocket gateway error!";
            }
          }
        });
      } catch (err) {
        asyncWrapper(()=>{self.loginFailed(err);})
      }
    },

    /**
     * 
     * @params {*} lifecycle 
     * @params {*} params 
     */
    onLoginSuccess: function(lifecycle, params) {
      if (!Store.restoring) {
        eventRegistry.trigger(EventType.EVENT_LOGIN, EventResult.SUCCESS, params);
      } else {
        asyncWrapper(()=>{this.doJoin(Store.userInfo.groupId)})
      }
    },

    /**
     * 
     * @params {*} lifecycle 
     * @params {*} params 
     */
    onLoginFailed: function(lifecycle, params) {
      logger.error("Login failed: " + params);
      if (!Store.restoring) {
        eventRegistry.trigger(EventType.EVENT_LOGIN, EventResult.FAILED);
      } else {
        Store.restoring = false
        logger.warn("Restoring failed for login!")
      }
    },

    /**
     * 
     * @params {*} lifecycle 
     * @params {*} params 
     */
    onDoJoin: function(lifecycle, params) {
      protocolSender.sendJoinGroupReq(params);
      Store.userInfo.groupId = params;
    },

    /**
     * 
     * @params {*} lifecycle 
     * @params {*} params 
     */
    onJoinSuccess: function(lifecycle, params) {
      if (!Store.restoring) {
        eventRegistry.trigger(EventType.EVENT_JOIN_GROUP, EventResult.SUCCESS, params);
      } else {
        // Store.restoring = false
        logger.log("Restoring success.")
      }
    },

    /**
     * 
     * @params {*} lifecycle 
     * @params {*} params 
     */
    onJoinFailed: function(lifecycle, params) {
      if (!Store.restoring) {
        eventRegistry.trigger(EventType.EVENT_JOIN_GROUP, EventResult.FAILED);
      } else {
        Store.restoring = false
        logger.warn("Restoring failed for join group!")
      }
    },

    /**
     * 
     * @params {*} lifecycle 
     * @params {*} params 
     */
    onDoLeave: function(lifecycle, params) {
      protocolSender.sendLeaveGroupReq();
    },

    /**
     * 
     * @params {*} lifecycle 
     * @params {*} params 
     */
    onLeaveSuccess: function(lifecycle, params) {
      this.onLeaveGroup();
      eventRegistry.trigger(EventType.EVENT_LEAVE_GROUP, EventResult.SUCCESS);
    },

    /**
     * 
     * @params {*} lifecycle 
     * @params {*} params 
     */
    onLeaveFailed: function(lifecycle, params) {
      eventRegistry.trigger(EventType.EVENT_LEAVE_GROUP, EventResult.FAILED);
    },

    /**
     * 
     * @params {*} lifecycle 
     * @params {*} params 
     */
    onDoLogout: function(lifecycle, params) {
      protocolSender.sendLogoutReq();
    },

    /**
     * 
     * @params {*} lifecycle 
     * @params {*} params 
     */
    onLogoutSuccess: function(lifecycle, params) {
      this.onLogout();
      eventRegistry.trigger(EventType.EVENT_LOGOUT, EventResult.SUCCESS);
    },

    /**
     * 
     * @params {*} lifecycle 
     * @params {*} params 
     */
    onLogoutFailed: function(lifecycle, params) {
      eventRegistry.trigger(EventType.EVENT_LOGOUT, EventResult.FAILED);
    },

    /**
     * 
     * @params {*} lifecycle 
     * @params {*} params 
     */
    onDoDestroy: function(lifecycle, params) {
      this.onDestroy();
    },

    /**
     * 
     * @param {*} lifecycle 
     * @param {*} params 
     */
    onRestoring: function(lifecycle, params) {
      // Store.restoring = true
      // asyncWrapper(()=>{this.doInit()})
    }
  }
});

export default RtcEngine;
