

/**
 * 
 * @param {*} target
 * @param {*} p0 
 * @param {*} p1 
 */
function isPointAt (target, p0, p1) {
  let maxAllowOffsetLength = 15
  const A = p1.y- p0.y
  const B = p0.x - p1.x
  const C = p1.x * p0.y - p0.x* p1.y
  // 
  let dis = Math.abs(A * target.x + B * target.y + C) / Math.sqrt(Math.pow(A, 2) + Math.pow(A, 2))
  if (dis > maxAllowOffsetLength) {
    return false
  } else {
    const D = (A * target.y - B * target.x);
    const jiaoX = -(A * C + B *D) / (Math.pow(B, 2) + Math.pow(A, 2));
    //jiaoXt0~11p10p0
    const t = (jiaoX - p0.x) / (p1.x - p0.x);
    if (t > 1  || isNaN(t)) {
    //p1
      dis = getLengthOfTwoPoint(p1, target);
    }else if (t < 0){
    //p2
      dis = getLengthOfTwoPoint(p0, target);
    }
    //
    if (dis <= maxAllowOffsetLength) {
      return true;
    } else {
      return false;
    }
  }
}
//
function getLengthOfTwoPoint(point1, point2){
  return Math.sqrt(Math.pow(point1.x - point2.x, 2) + Math.pow(point1.y - point2.y, 2));
}

export {
  isPointAt
}