import Store from "../../../util/store"
import { Graphics } from "../../../../common/enums"
import eventRegistry from "../../../../common/event-registry"

export default class WriteBoard{
  constructor (painter, pageStore) {
    // super(params)
    this.painter = painter
    this.pageStore = pageStore || {}
    this.render = this.painter.render
    this.userId = Store.userInfo.userId
    this.parttern = {}
   
    this.getGraphId()
    
    // 
    this.graphChildren = []
    // 
    this.boundingRect = null
    // 
    this.moveStatus = false

    this.registryTouchEvent()
  }

  registryTouchEvent() {
    const startId = `touchStart${this.painter.mediaId}`
    const moveId = `touchMove${this.painter.mediaId}`
    const endId = `touchEnd${this.painter.mediaId}`
    // callBack
    eventRegistry.off(startId)
    eventRegistry.off(moveId)
    eventRegistry.off(endId)
    eventRegistry.on(startId, this.touchMoveStart.bind(this))
    eventRegistry.on(moveId, this.touchMoveEvent.bind(this))
    eventRegistry.on(endId, this.touchMoveEnd.bind(this))
  }

  // id
  getGraphId () {
    return ((this.userId << 16) | this.pageStore.graphIdSeed ++)
  }
  /**
   * canvas
   */
  touchMoveStart (e) {
    this.rateX = this.painter.width / this.painter.canvasWidth
    this.rateY = this.painter.height / this.painter.canvasHeight
    let canvasDis = this._calcXY(e.touches[0].x, e.touches[0].y)
    // 
    if (this.pageStore.shape === Graphics.ERASER) {
      let pageData = this.pageStore.findPagePaints(this.pageStore.getCurPage())
      // 
      let paints = this.pageStore.findPaintsByPoint(pageData, canvasDis)
      if (paints) {
        this.painter._sendGraphMsg(paints.type, paints[paints.type], 3)
        this._storeStack(paints.type, paints[paints.type])
      }
      return
    }
    this._createShapeObject()
    // 
    if (this.pageStore.shape === Graphics.CURVE || this.pageStore.shape === Graphics.MASK_CURVE) {
      this.parttern.points.push([canvasDis.x, canvasDis.y])
      this.painter._sendGraphMsg(this.pageStore.shape, this.parttern, 1)
    }
  }

  /**
   * canvas
   */
   touchMoveEvent (e) {
    const endx = Math.floor(e.touches[0].x * this.rateX)
    const endy = Math.floor(e.touches[0].y * this.rateY)
    let canvasDis = this._calcXY(e.touches[0].x, e.touches[0].y)
    // 
    if (this.pageStore.shape === Graphics.ERASER) {
      let pageData = this.pageStore.findPagePaints(this.pageStore.getCurPage())
      // 
      let paints = this.pageStore.findPaintsByPoint(pageData, canvasDis)
      if (paints) {
        this.painter._sendGraphMsg(paints.type, paints[paints.type], 3)
        this._storeStack(paints.type, paints[paints.type])
      }
    } else if (this.pageStore.shape === Graphics.LINE) {
      this.parttern.x2 = endx
      this.parttern.y2 = endy
      this.painter._sendGraphMsg(this.pageStore.shape, this.parttern, 2)
    } else if (this.pageStore.shape === Graphics.CURVE) {
      this.parttern.points = [[endx, endy]]
      this.painter._sendGraphMsg(this.pageStore.shape, this.parttern, 2)
    } else if(this.pageStore.shape === Graphics.GMOV) {
      if (this.moveStatus) {
        this.parttern.ox = endx - canvasDis.x
        this.parttern.oy = endy - canvasDis.y
        this.parttern.graphs = this.graphChildren
        this.painter._sendGraphMsg(Graphics.GMOV, this.parttern)
        canvasDis.x = endx
        canvasDis.y = endy
      } else {
        this.painter.render._drawAllPaint()
        this.boundingRect =  {x1: canvasDis.x, y1: canvasDis.y, x2: endx,y2: endy}
        this.render.drawBoundingRect(this.boundingRect)
      }
    }
  }

  /**
   * canvas
   */
   touchMoveEnd (e) {
    // 
    if (this.pageStore.shape !== Graphics.GMOV) {
      this._storeStack(this.pageStore.shape, this.parttern)
      this.painter._sendGraphMsg(this.pageStore.shape, this.parttern, 5)
    }
    if (this.pageStore.shape === Graphics.GMOV) {
      this.moveStatus = false
      this.render._drawAllPaint()
      if (this.boundingRect) {
        this.graphChildren = this.pageStore.containPaintObj(this.boundingRect)
        this.boundingRect = this.pageStore.groupBoundingRect(this.graphChildren)
        // 
        this.render.drawBoundingRect(this.boundingRect)
      }
    }
  }


  _storeStack (shape, parttern) {
    let params = {
      type: shape,
    }
    params[shape] = parttern
    this.pageStore.revokeStack.push(params)
  }
  /**
   * 
   */
  _createShapeObject () {
    switch (this.pageStore.shape) {
      case Graphics.LINE: this.parttern = {
          pid: this.pageStore.getCurPage(),
          gid: this.getGraphId(),
          action: null,
          graph: 1,
          brush_size: this.pageStore.brush_size,
          color: this.pageStore.color,
          x1: null,
          y1: null,
          x2: null,
          y2: null
        }
        break;
      case Graphics.CURVE:
        this.parttern = {
          pid: this.pageStore.getCurPage(),
          gid: this.getGraphId(),
          action: null,
          graph: 2,
          brush_size: this.pageStore.brush_size,
          color: this.pageStore.color,
          points: []
        }
        break;
      case Graphics.MASK_CURVE: 
        this.parttern = {
          pid: this.pageStore.getCurPage(),
          gid: this.getGraphId(),
          action: null,
          graph: 2,
          brush_size: this.pageStore.brush_size,
          color: this.pageStore.color,
          points: []
        }
        break;
      case Graphics.TEXT: this.parttern = {
          pid: this.pageStore.getCurPage(),
          gid: this.getGraphId(),
          text: '',
          action: null,
          color: this.pageStore.color,
          left: '',
          top: '',
          right: '',
          bottom: '',
          font: '',
          fontsize: 20,
        }
        break
      case Graphics.GMOV: this.parttern = {
          pid: this.pageStore.getCurPage(),
          ox: null,
          oy: null,
          graphs: []
      }
    }
  }
  /**
   * 
   * @param {*} x 
   * @param {*} y 
   */
  _calcXY (x, y) {
    return {
      x: Math.ceil(this.rateX * x),
      y: Math.ceil(this.rateY * y)
    }
  }
  /**
   * 
   * @param {*} x 
   * @param {*} y 
   */
  _calcTextDisance (x, y) {
    //  
    if (
      this.painter.displayMode == DisplayMode.DBSY) {
      return {
        x,
        y
      }
    } else {
      return {
        x: Math.ceil(x + (this.painter.parentEle.offsetWidth - this.painter.canvasWidth) / 2),
        y: Math.ceil(y + (this.painter.parentEle.offsetHeight - this.painter.canvasHeight) / 2)
      }
    }
   
  }
}