import logger from "../../common/logger"
import MsgDispatcher from '../util/msg-dispatcher'
import { ProxyEventType } from '../../common/enums'
import eventRegistry from "../../common/event-registry"
import store from "./store"

/**
 * websocket
 */
class HstWebSocket {
  constructor() {
    this.curAddrUrl = ''
    this.socket = {}
    this.allSocket = {}
    this.handlers = [] // 
    this.connected_hooks = [] // socket
    this.closed_hooks = [] // socket
    this.error_hooks = []
    this.limitConnect = this.getTimer()
    this.proxyIndex = 0
    this.timer = null
    this.lockReconnect = false
    const msgDispatcher = new MsgDispatcher()
    msgDispatcher.addMsgHandler(ProxyEventType.CREATE_CHANNEL_SUCCESS, this._onConnectSuccess.bind(this))
    this.handlers.push(msgDispatcher)
  }

  /**
   * websocket
   */
  _createWebsocket(key) {
    let self = this
    const { addrUrl } = this.allSocket[key]
    this.curAddrUrl = addrUrl
    if (!addrUrl) {
      throw new Error("Can't find webscoket link !");
    }
    let options = {
      url: addrUrl,
      success: function() {
        logger.info("Create websocket success.")
      },
      fail: function(res) {
        logger.error("Create websocket failed, url: " + self.curAddrUrl);
        console.error(res)
      },
      complete: function() {
        logger.info("Create websocket complete.");
      }
    }
    this.socket = wx.connectSocket(options);
    if (!this.socket) {
      logger.error("Connect socket failed: " + this.curAddrUrl);
      return;
    }
    this._eventBind(key);
  }

  _sendCreateProxyMsg(key) {
    const { backward_addr } =  this.allSocket[key]
    const msg = {
      id: ProxyEventType.CREATE_CHANNEL,
      backward_addr: backward_addr,
      seq: key
    }
    if (this.socket && this.socket.readyState === 1) {
      this.socket.send({
        data: JSON.stringify(msg),
        success: (res) => {
        },
        fail: function(res) {
          console.warn('res', res)
          logger.error("Send failed!");
          resolve(false)
        },
        complete: function(res) {
        }
      })
    } else {
      logger.warn('socket error')
    }
  }

  /**
   * 
   * @param {*} socket 
   */
  _eventBind(key) {
    let self = this
    this.socket.onOpen(function(res){
      logger.warn("Websocket opened: " + self.curAddrUrl);
      this.proxyIndex = 1
      console.error(res)
      self._sendCreateProxyMsg(key)
    })

    this.socket.onClose(function(res){
      logger.warn("Websocket closed: " + self.curAddrUrl);
      console.error(res)
      self.closed_hooks.map(item => {
        item(arguments)
      })
      self.handlers = []
      self.connected_hooks = []
      self.closed_hooks = []
      self.error_hooks = []
      if (res.code !== 1000) {
        eventRegistry.trigger('onError', { code: 101 })
      }
    })

    this.socket.onMessage(function(res) {
      let msg = JSON.parse(res.data);
      const { channel_id } = msg
      if (channel_id == store.mainWebsocketChannelId && msg.id == ProxyEventType.CONNECT_ERROR && msg.result == 2) {
        eventRegistry.trigger('onError', { code: 101 })
        if (self.socket !== undefined) {
          self.socket.close()
        }
      }
      if (msg.id == ProxyEventType.BUSINESS_MESSAGE) {
        msg.msg && (msg = JSON.parse(msg.msg))
      }
      msg.channel_id = channel_id
      if (msg.id != 30000) { // 
        logger.log("===> Recv msg from " + self.curAddrUrl + ": " + JSON.stringify(msg));
      }
      self.handlers.map(handler => {
        handler.onMessage(msg)
      });
    });

    this.socket.onError(function(res){
      logger.warn('Error res', res)
      self.error_hooks.map(item => {
        item(arguments)
      })
      if (self.socket !== undefined) {
        self.socket.close()
      }
    })
  }

  _onConnectSuccess() {
    this.connected_hooks.map(item => {
      item(arguments)
    })
  }

  createProxyConnect(options) {
    const key = options.seq_id
    if (this.allSocket[key]) {
      logger.info('Proxy connect already exists !')
    } else {
      const { handler = {}, opened = ()=>{}, closed = ()=>{}, error = ()=>{} } = options
      this.handlers.push(handler)
      this.connected_hooks.push(opened)
      this.closed_hooks.push(closed)
      this.error_hooks.push(error)
      this.allSocket[key] = {...options}
      if (this.socket && this.socket.readyState === 1) {
        this._sendCreateProxyMsg(key)
      } else {
        this._createWebsocket(key)
      }
    }
  }
  
  /**
   * 
   */
   reconnect() {
     console.warn('start reconnect')
     if (this.lockReconnect) return
     this.lockReconnect = true
     clearTimeout(this.timer)
     this.timer = null;
     if (this.proxyIndex < 2) {
        this.timer = setTimeout(()=> {
          this.lockReconnect = false
          for(const key in this.allSocket) {
            if (this.socket && this.socket.readyState === 1) {
              this._sendCreateProxyMsg(key)
            } else {
              this._createWebsocket(key)
            }
          }
        }, 5000)
        this.proxyIndex ++
     } else {
      clearInterval(this.timer)
      this.timer = null;
      throw new Error('')
     }
   }
  // reconnect() {
  //   if (!this.connectStatus) return
  //   this.limitConnect(function() {
  //     this.proxyIndex++;
  //   });

  //   this.createProxyConnect();
  // }

  // /**
  //  * websocket
  //  */
  // getSocket() {
  //   if (this.socket && this.socket.readyState === 1) {
  //     return this.socket;
  //   }
  //   return null;
  // }

  /**
   * 
   */
  getTimer() {
    var timerArray = [];
    return function(fn) {
      timerArray.push(new Date().getTime());
      var timer = new Date().getTime();
      if (timer - timerArray[0] > 5000) {
        timerArray = [];
        fn.apply(this, arguments);
      }
    };
  }

  // /**
  //  * 
  //  * @param {*} val 
  //  */
  // setAddr(val) {
  //   if (!(val instanceof Array)) {
  //     throw new Error("value is not Array");
  //   }
  //   this.addrUrls = val;
  //   this.proxyIndex = 0;
  //   this.createWebsocket();
  // }

  /**
   * 
   * @param {*} msg 
   */
  send(msg) {
    if (this.socket && this.socket.readyState === 1) {
      logger.log("<=== Send msg to " + this.curAddrUrl + ": " + msg);
      this.socket.send({
        data: msg,
        success: function(res) {
        },
        fail: function(res) {
          logger.error("Send failed!");
        },
        complete: function(res) {
        }
      });
    } else {
      logger.error("Send msg failed: " + msg);
    }
  }

  /**
   * websocket
   */
  close() {
    if (this.socket) {
      this.socket.close();
      this.socket = null;
    }
  }
}
export default new HstWebSocket()
