import { getCtrlPoint, stringToColor, stringToRGBA } from '../../common/common'
import { fillMode, fillStyle } from '../../common/enums'
import wbManager from "../media/white-board-manager"

/**
 * 
 * @param {*} canv 
 * @param {*} lineType 0 1 2 3 4
 */
 function setLineDash (canv, lineType, ratio) {
  let width1 = 20 * ratio
  let width2 = 5 * ratio
  switch (lineType) {
    case 0: 
      canv.setLineDash([]);
      break;
    case 1: 
      canv.setLineDash([width1, width2]);
      break;
    case 2: 
      canv.setLineDash([width2, width2]);
      break;
    case 3: 
      canv.setLineDash([width1, width2, width2, width2]);
      break;
    case 4:
      canv.setLineDash([width1, width2, width2, width2, width2, width2]);
      break;
    default: 
      canv.setLineDash([]);
  }
}
/**
 * 
 * @param {*} canv 
 * @param {*} obj 
 * @param {*} ratio 
 * @param {*} type 
 */
function fillCustom (canv, obj, ratio, type) {
  canv.clip() //
  canv.beginPath()
  canv.lineCap = "butt"; // 
  canv.lineWidth = 1; // 
  canv.setLineDash([]);
  canv.strokeStyle = stringToColor(obj.bg_color); // 
  let x1 = Math.ceil(obj.left * ratio)
  let y1 = Math.ceil(obj.bottom * ratio)
  let x2 = Math.ceil(obj.right * ratio)
  let y2 = Math.ceil(obj.top * ratio)
  let width = x2 - x1
  let height = y2 - y1
  let space = 8
  let n = Math.ceil(width / space)
  let m = Math.ceil(height / space)
  x2 = x1 + (n + m) * space
  y2 = y1 + (m + n) * space
  switch (type) {
    case fillStyle.SOLID:
      canv.beginPath()
      canv.fillRect(x1, y1, width, height);
      canv.stroke()
      canv.closePath()
      break;
    case fillStyle.L_SLASH: 
      for (let i = 1; i <= m + n; i++) {
        canv.beginPath()
        canv.moveTo(x1, y1 + space * i)
        canv.lineTo(x1 + space * i, y1)
        canv.stroke()
        canv.closePath()
        canv.beginPath()
        canv.moveTo(x2, y2 - space * i)
        canv.lineTo(x2 - space * i, y2)
        canv.stroke()
        canv.closePath()
      }
      break;
    case fillStyle.GRID: 
      for (let i = 1; i < n; i++) { // 
        canv.beginPath()
        canv.moveTo(x1 + space * i, y1)
        canv.lineTo(x1 + space * i, y2)
        canv.stroke()
        canv.closePath()
      }
      for (let i = 1; i < m; i++) { // 
        canv.beginPath()
        canv.moveTo(x1, y1 + space * i)
        canv.lineTo(x2, y1 + space * i)
        canv.stroke()
        canv.closePath()
      }
      break;
    case fillStyle.IN_GRID: 
      for (let i = 1; i <= m + n; i++) {
        canv.beginPath()
        canv.moveTo(x1, y1 + space * i)
        canv.lineTo(x1 + space * i, y1)
        canv.stroke()
        canv.closePath()
        canv.beginPath()
        canv.moveTo(x2, y2 - space * i)
        canv.lineTo(x2 - space * i, y2)
        canv.stroke()
        canv.closePath()
      }
      for (let i = 1; i <= m + n; i++) {
        canv.beginPath()
        canv.moveTo(x2 - space * i, y1)
        canv.lineTo(x2, y1 + space * i)
        canv.stroke()
        canv.closePath()
        canv.beginPath()
        canv.moveTo(x1, y1 + space * i)
        canv.lineTo(x2 - space * i, y2)
        canv.stroke()
        canv.closePath()
      }
      break;
    case fillStyle.R_SLASH: 
      for (let i = 1; i <= m + n; i++) {
        canv.beginPath()
        canv.moveTo(x2 - space * i, y1)
        canv.lineTo(x2, y1 + space * i)
        canv.stroke()
        canv.closePath()
        canv.beginPath()
        canv.moveTo(x1, y1 + space * i)
        canv.lineTo(x2 - space * i, y2)
        canv.stroke()
        canv.closePath()
      }
      break;
    case fillStyle.VERTICAL:
      for (let i = 1; i < n; i++) { // 
        canv.beginPath()
        canv.moveTo(x1 + space * i, y1)
        canv.lineTo(x1 + space * i, y2)
        canv.stroke()
        canv.closePath()
      }
      break;
    case fillStyle.HORIZONTAL:
      for (let i = 1; i < m; i++) { // 
        canv.beginPath()
        canv.moveTo(x1, y1 + space * i)
        canv.lineTo(x2, y1 + space * i)
        canv.stroke()
        canv.closePath()
      }
      break;
    default: 
      canv.fill()
  }
}
/**
 * 
 * @param {*} canv 
 * @param {*} obj  fill_mode 
 * @param {*} ratio 
 */
function drawFillMode (canv, obj, ratio) {
  if (obj.fill_mode === undefined || obj.fill_mode === fillMode.EMPTY) {
    canv.stroke();
  } else if (obj.fill_mode === fillMode.FILL_WITH_BORDER){
    canv.stroke();
    fillCustom(canv, obj, ratio, obj.bg_shape)
  } else if (obj.fill_mode === fillMode.FILL_NO_BORDER) {
    fillCustom(canv, obj, ratio, obj.bg_shape)
  }
}

const draw = {
  // 
  line: (canv, obj, ratio) => {
    canv.beginPath();
    setLineDash(canv, obj.line_shape, ratio)
    canv.lineCap = "butt"; // 
    canv.lineWidth = parseInt(obj.brush_size * ratio); // 
    canv.strokeStyle = stringToColor(obj.color); // 

    canv.moveTo(obj.x1 * ratio, obj.y1 * ratio)
    canv.lineTo(obj.x2 * ratio, obj.y2 * ratio)

    canv.stroke();
    canv.closePath();
  },
  // 
  curve: (canv, obj, ratio) => {
    canv.beginPath();
    setLineDash(canv, obj.line_shape, ratio)
    canv.lineCap = "butt"; // 
    canv.lineWidth = obj.brush_size * ratio; // 
    canv.strokeStyle = stringToColor(obj.color); // 
    const points = obj.points.map((item) => {
      return {
        X: parseInt(item[0] * ratio),
        Y: parseInt(item[1] * ratio)
      }
    })
    if (obj.brush_size > 4) {
      canv.moveTo(points[0].X, points[0].Y);
      for (let i = 1; i < points.length; i++) {
        let ctrlP = getCtrlPoint(points, i - 1)
        canv.bezierCurveTo(ctrlP.pA.x, ctrlP.pA.y, ctrlP.pB.x, ctrlP.pB.y, points[i].X, points[i].Y)
      }
    } else {
      for (let i = 0; i < points.length; i++) {
        canv.lineTo(points[i].X, points[i].Y)
      }
    }
    canv.stroke()
    canv.closePath()
  },
  // 
  text: (canv, obj, ratio) => {
    if (!Object.prototype.hasOwnProperty.call(obj, 'charset')) {
      canv.beginPath();
      canv.font = `${obj.fontsize * 4 / 3 * ratio}px "${obj.color}"`;
      canv.textBaseline = "top";
      canv.fillStyle = stringToColor(obj.color);
      var indexLine = 0;
  
        for (var txt of obj.text.split('\r\n')) {
          canv.fillText(txt, obj.left * ratio, (indexLine * 22) + obj.top * ratio);
          indexLine++;
        }
    } else { //  fix bug #2201
      draw.cloudText(canv, obj, ratio)
    }
    canv.closePath();
  },
 
  cloudText: (canv, obj, ratio) => {
    let fontsize = parseInt((obj.fontsize + '').replace('-', ''))
    canv.font = `${fontsize * ratio}px ${obj.font}`
    canv.fillStyle = stringToColor(obj.color)
    canv.textBaseline = "middle"
    let left = obj.left * ratio
    let bottom = (obj.bottom + fontsize - 10) * ratio
    let maxWidth = (obj.right - obj.left) * ratio
    var strArr = obj.text.split(/\r\n|\n/)
    var row = 0
    for (var i = 0; i < strArr.length; i++) {
      if (strArr[i] == '') {
        row++
      } else {
        var lineBreakNum = drawText(canv, strArr[i], left, bottom,  maxWidth, row, fontsize)
        row = row + lineBreakNum
      }
    }
    function drawText (ctx, str, x1, y1, maxWidth, row, fontsize) {
      str = str.split('')
      let temp = ""     
      let rowTextArr = [] // 
      for (var i = 0; i < str.length; i++) {
        // 
        if (ctx.measureText(temp + str[i]).width <= maxWidth) {
          temp += str[i]
        } else {
          rowTextArr.push(temp)
          temp = str[i]
        }
      }
      rowTextArr.push(temp)
      for (var j = 0; j < rowTextArr.length; j++) {
        ctx.fillText(rowTextArr[j], x1, y1 + (j + row) * fontsize * ratio)
      }
      return rowTextArr.length
    }
  },
  // 
  rectangle: (canv, obj, ratio) => {
    canv.beginPath()
    canv.lineWidth = obj.brush_size * ratio // 
    canv.strokeStyle = obj.color // 

    let x1 = parseInt(obj.x1) * ratio;
    let y1 = parseInt(obj.y1) * ratio;
    let x2 = parseInt(obj.x2) * ratio;
    let y2 = parseInt(obj.y2) * ratio;

    canv.rect(x1, y1, (x2 - x1), (y2 - y1));
    canv.closePath();
    canv.stroke();
  },
  // 
  mask_curve: (canv, obj, ratio) => {
    canv.beginPath();
    setLineDash(canv, obj.line_shape, ratio)
    canv.lineCap = "butt"; // 
    canv.lineWidth = obj.brush_size * ratio; // 
    canv.strokeStyle = stringToRGBA(stringToColor(obj.color), '.4'); // 
    const points = obj.points.map((item) => {
      return {
        X: parseInt(item[0] * ratio),
        Y: parseInt(item[1] * ratio)
      }
    })
    if (obj.brush_size > 4) {
      canv.moveTo(points[0].X, points[0].Y);
      for (let i = 1; i < points.length; i++) {
        let ctrlP = getCtrlPoint(points, i - 1)
        canv.bezierCurveTo(ctrlP.pA.x, ctrlP.pA.y, ctrlP.pB.x, ctrlP.pB.y, points[i].X, points[i].Y)
      }
    } else {
      for (let i = 1; i < points.length; i++) {
        canv.lineTo(points[i].X, points[i].Y)
      }
    }
    canv.stroke()
    canv.closePath()
  },
  // 
  arrow: (canv, obj, ratio) => {
    setLineDash(canv, obj.line_shape, ratio)
    canv.lineWidth = obj.brush_size * ratio // 
    canv.strokeStyle = stringToColor(obj.color) // 
    canv.fillStyle = stringToColor(obj.color)

    drawLineArrow(canv, obj.x1 * ratio, obj.y1 * ratio, obj.x2 * ratio, obj.y2 * ratio, 10, 26)

    /**
     * 
     * fromX / fromY 
     * toX / toY 
     * headlen 
     * theta 
     */
    function drawLineArrow (ctx, fromX, fromY, toX, toY, headlen, theta) {
      var arrowX, arrowY // 
      // 
      var angle = Math.atan2(fromY - toY, fromX - toX) * 180 / Math.PI
      var angle1 = (angle + theta) * Math.PI / 180
      var angle2 = (angle - theta) * Math.PI / 180
      var topX = headlen * Math.cos(angle1)
      var topY = headlen * Math.sin(angle1)
      var botX = headlen * Math.cos(angle2)
      var botY = headlen * Math.sin(angle2)

      canv.save()
      ctx.beginPath()

      //
      ctx.moveTo(fromX, fromY)
      ctx.lineTo(toX, toY)
  
      arrowX = toX + topX
      arrowY = toY + topY

      //
      ctx.moveTo(arrowX, arrowY)
      ctx.lineTo(toX, toY)
  
      arrowX = toX + botX
      arrowY = toY + botY

      //
      ctx.lineTo(arrowX, arrowY)

      ctx.closePath()
      ctx.fill()
      ctx.stroke()
      ctx.restore()
    }
  },
  rect: (canv, obj, ratio) => {
    canv.save()
    canv.beginPath()
    if (obj.bg_color) {
      canv.fillStyle = stringToColor(obj.bg_color)
    }
    setLineDash(canv, obj.line_shape, ratio)
    canv.lineWidth = obj.brush_size * ratio // 
    canv.strokeStyle = stringToColor(obj.color) // 

    let x1 = parseInt(obj.left) * ratio
    let y1 = parseInt(obj.bottom) * ratio
    let x2 = parseInt(obj.right) * ratio
    let y2 = parseInt(obj.top) * ratio

    canv.rect(x1, y1, (x2 - x1), (y2 - y1));
    canv.closePath();
    drawFillMode(canv, obj, ratio)
    canv.restore()
  },
  // 
  round_rect: (canv, obj, ratio) => {
    canv.save()
    setLineDash(canv, obj.line_shape, ratio)
    canv.lineWidth = obj.brush_size * ratio // 
    canv.strokeStyle = stringToColor(obj.color) // 

    let x1 = parseInt(obj.left) * ratio //  x 
    let y1 = parseInt(obj.bottom) * ratio //  y
    let width = parseInt(obj.right - obj.left) * ratio // 
    let height = parseInt(obj.top - obj.bottom) * ratio //  
    let radius = Math.min(width, height) / 10 // 

    canv.save()
    canv.translate(x1, y1)
    canv.beginPath()
    // 0  1 / 2PI  
    canv.arc(width - radius, height - radius, radius, 0, Math.PI / 2)
    //
    canv.lineTo(radius, height)
    // 1 / 2PI  PI
    canv.arc(radius, height - radius, radius, Math.PI / 2, Math.PI)
    //
    canv.lineTo(0, radius)
    // PI  3 / 2PI  
    canv.arc(radius, radius, radius, Math.PI, Math.PI * 3 / 2)
    //
    canv.lineTo(width - radius, 0)
    //
    canv.arc(width - radius, radius, radius, Math.PI * 3 / 2, Math.PI * 2)
    //  
    canv.lineTo(width, height - radius)
    canv.closePath()
    canv.restore() // translate 

    drawFillMode(canv, obj, ratio)
    canv.restore()
  },
  // 
  ellipse: (canv, obj, ratio) => {
    canv.save()
    setLineDash(canv, obj.line_shape, ratio)
    canv.lineWidth = obj.brush_size * ratio // 
    canv.strokeStyle = stringToColor(obj.color) // 

    let a = parseInt((obj.right - obj.left) / 2) * ratio // 
    let b = parseInt((obj.top - obj.bottom) / 2) * ratio // 
    let x = a + parseInt(obj.left) * ratio //  x 
    let y = b + parseInt(obj.bottom) * ratio //  y 

    let r = (a > b) ? a : b // abarc
    let ratioX = a / r // 
    let ratioY = b / r // 
    canv.save()
    canv.scale(ratioX, ratioY) // 
    canv.beginPath()
    canv.moveTo((x + a) / ratioX, y / ratioY) //
    canv.arc(x / ratioX, y / ratioY, r, 0, 2 * Math.PI)
    canv.closePath()
    canv.restore() // 

    drawFillMode(canv, obj, ratio)

    canv.restore()
  },
  // 
  pic: (canv, obj, ratio, canvasEle) => {
    let filePath =  `/${obj.user_data.id}`
    let key2 = `/${obj.user_data.id}/${0}.jpg`
    // sendMessage(key2)
    if (!obj.insertSuccess) {
      insertImage()
    }
    function insertImage (n = 0) {
      if (n > 20) return
      if (wbManager.cacheFileUrlMap.get(filePath) && wbManager.cacheFileUrlMap.get(filePath).get(key2)) {
        let left = obj.left * ratio
        let bottom = obj.bottom * ratio
        let right = obj.right * ratio
        let top = obj.top * ratio
        wx.getImageInfo({
          src: wbManager.cacheFileUrlMap.get(filePath).get(key2),
          success: function(res) {
            console.log('getImageInfoRes', res)
            if (canvasEle) {
              let seal = canvasEle.createImage()
              seal.src = res.path
              seal.onload = function() {
                console.log("")
                canv.drawImage(
                    seal, 
                    0, 
                    0, 
                    right - left, 
                    top - bottom)
                obj.insertSuccess = true
              }
              seal.onerror = function(res) {
                console.error("", res)
              }
            } 
          },
          fail: function(err) {
            console.warn("Get image info failed!")
          }
        })
      } else {
        wbManager._getFileUrl(key2)
        setTimeout(() => {
          insertImage(++n)
        }, 500)
      }
    }
  }
}

