
import { Graphics } from "../../../common/enums"
import { isPointAt } from '../../util/math'
import { fromPoints, fromLine } from '../../../core/bbox'
/**
 * 
 */
class PageStore {
  constructor(data) {
    /**
     * 
     * boardData : {
     *     seq_id: '' // 
     *     board_name: 'xxx',
     *     board_type: 'xxx',
     *     "width":1280,
     *     "height":720,
     *     "page":10,
     *     "current_pid":2,
     *     "file_path":,
     *     "convert_file_path",
     *     paintMap:   // map key:  value: [obj]  
     * }
     */
    this.boardData = { paintMap: new Map() }
    // 
    this.editConfig = {
      id: 1, 
      status: false
    }
    this.shape = Graphics.CURVE
    this.color  = 4278190080
    this.brush_size = 4
    // 
    this.revokeStack = []
    // 
    this.restoreStack = []
    // 
    this.graphIdSeed = 0

    this._init(data)
    return this
  }

  /**
   * 
   * @param {*} data 
   */
  _init(data) {
    // 
    let pageData = data.slice(0, 1)

    // 
    let paintObjs = data.slice(1)

    // 
    this.boardData = Object.assign(this.boardData, pageData[0].board)
    this.editConfig.id = this.boardData.edit_id

    // 
    if (paintObjs.length > 0) {
      paintObjs.map((item) => {
        let items = this.boardData.paintMap.get(item[item.type].pid)
        if (!items) {
          this.boardData.paintMap.set(item[item.type].pid, [item])
          return
        }
        items.push(item)
        this.boardData.paintMap.set(item[item.type].pid, items)
      })
    } else {
      this.boardData.paintMap.set(pageData[0].board.current_pid, [])
    }

  }
  clearPaints () {
    this.boardData.paintMap = new Map()
    this.boardData.paintMap.set(this.getCurPage(), [])
  }

  /**
   * 
   */
  getCurPage () {
    return this.boardData.current_pid
  }

  /**
   * 
   */
  getPageCount() {
    return this.boardData.page;
  }

  /**
   * TODO
   */
  getFilePath() {
    return this.boardData.convert_file_path;
  }

  /**
   * URL
   */
  getConvertFilePath() {
    return this.boardData.convert_file_path
  }

  /**
   * 
   * @param {*} pid 
   */
  setCurPage (pid) {
    this.boardData.current_pid = pid
  }

  /**
   * 
   */
  getBoardName() {
    return this.boardData.board_name;
  }

  /**
   * 
   * @param {*} item 
   */
  deletePage (item) {
    let items = this.boardData.paintMap.get(item[item.type].pid)
    if (items) {
      this.boardData.paintMap.set(item[item.type].pid, [])
    }
  }

  /**
   * 
   * @param {*} pid 
   */
  findPagePaints(pid) {
    if (!this.boardData.paintMap.get(pid)) {
      this.boardData.paintMap.set(pid, [])
    }
    return this.boardData.paintMap.get(pid)
  }

  /**
   * gid
   * @param {*} pageData 
   * @param {*} gid 
   */
  findPaintByGid (pageData, gid) {
    for (var i = 0; i < pageData.length; i++) {
      let data = pageData[i]
      // ObjId
      if (data[data.type].gid === gid) {
        // IDObj
        return data
      }
    }
    return null
  }

  /**
   * 
   * @param {*} pageData 
   * @param {*} paintObj
   */
  deletePaintObj(pageData, gid) {
    for (var i = 0; i < pageData.length; i++) {
      let data = pageData[i]
      // ObjId
      if (data[data.type].gid === gid) {
        // IDObj
        pageData.splice(i, 1)
        console.log('===>', pageData)
        break
      }
    }
  }

  /**
   * 
   * @param {*} pageData 
   * @param {*} paintObj 
   */
  addPaintObj (pageData, paintObj) {
    pageData.push(paintObj)
  }

  /**
   * 
   * @param {*} pageData 
   * @param {*} paintObj 
   */
  amendPaintObj(pageData, paintObj) {
    for (var i = 0; i < pageData.length; i++) {
      let data = pageData[i]
      // ObjId
      if (data[data.type].gid === paintObj[paintObj.type].gid) {
        // IDObj
        if (data.type === Graphics.CURVE) {
          data[data.type].points.push(...paintObj[paintObj.type].points)
        } else {
          pageData.splice(i, 1, paintObj)
        }
        break
      }
    }
  }
  /**
   * 
   * @param {*} pageData 
   * @param {*} gid 
   * @param {*} paintData 
   */
  repalcePaintObj (pageData, gid, paintData) {
    for (var i = 0; i < pageData.length; i++) {
      let data = pageData[i]
      if (data[data.type].gid === gid) {
        data[data.type] = paintData
        break
      }
    }
  }
  /**
   * 
   * @param {*} pageData 
   * @param {*} gid 
   * @param {*} paintObj 
   */
  movePaintObj(pageData, gid, paintObj) {
    for (var i = 0; i < pageData.length; i++) {
      let data = pageData[i]
      // ObjId
      if (data[data.type].gid === gid) {
        // IDObj
        if (data.type === Graphics.CURVE) {
          data[data.type].points = data[data.type].points.map((item) => {
            return (item = [item[0] + paintObj.ox, item[1] + paintObj.oy])
          })
        } else if (data.type === Graphics.TEXT) {
          data[data.type].left += paintObj.ox
          data[data.type].top += paintObj.oy
        } else {
          data[data.type].x1 += paintObj.ox
          data[data.type].y1 += paintObj.oy
          data[data.type].x2 += paintObj.ox
          data[data.type].y2 += paintObj.oy
        }
        break
      }
    }
  }
  deletePaintObj(pageData, gid) {
    for (var i = 0; i < pageData.length; i++) {
      let data = pageData[i]
      // ObjId
      if (data[data.type].gid === gid) {
        try {
          return pageData.splice(i, 1)[0]
        } catch (er) {
          return null
        }
        
      }
    }
    return null
  }
  findPaintsByPoint (pageData, target) {
    let flag = false
    for (var i = 0; i < pageData.length; i++) {
      let data = pageData[i]
      if (data.type === Graphics.CURVE || data.type === Graphics.MASK_CURVE) {
        let points = data[data.type].points
        for(let j = 0; j < points.length - 1; j++) {
          let point0 = points[j]
          let point1 = points[j + 1]
          flag = isPointAt(target, {x: point0[0], y: point0[1]}, {x: point1[0], y: point1[1]})
          if (flag) break;
        }
      } else if (data.type === Graphics.LINE) {
        let points = data[data.type]
        flag = isPointAt(target, {x: points.x1, y: points.y1}, {x: points.x2, y: points.y2})
      }
      if (flag) return data
    }
    return null
  }
 
  containPaintObj (rect) {
    let pageData = this.findPagePaints(this.boardData.current_pid)
    let paints = []
    pageData.map((data) => {
      // IDObj
      if (data.type === Graphics.CURVE) {
        let isContain = true
        data[data.type].points.map((item) => {
          if (item[0] < rect.x1 || item[0] > rect.x2 || item[1] < rect.y1 || item[1] > rect.y2) {
            isContain = false
          }
        })
        if (isContain) {
          paints.push(data[data.type].gid)
        }
      } else if (data.type === Graphics.TEXT) {
        // data[data.type].left += paintObj.ox
        // data[data.type].top += paintObj.oy
      } else if (data.type === Graphics.LINE){
        let item = data[data.type]
        if (item.x1 < rect.x1 || item.x2 > rect.x2 || item.y1 < rect.y1 || item.y2 > rect.y2) return
        paints.push(data[data.type].gid)
      }
    })
    return paints
  }
  boundingRect (gid) {
    let pageData = this.findPagePaints(this.boardData.current_pid)
    let graph = this.findPaintByGid(pageData, gid)
    let min = []
    let max = []
    if (graph.type  === 'line') {
      let points = graph[graph.type]
      fromLine(points.x1, points.y1, points.x2, points.y2, min, max)
    } else if (graph.type === 'curve') {
      fromPoints(graph[graph.type].points, min, max)
    }
    return {
      x1: min[0],
      y1: min[1],
      x2: max[0],
      y2: max[1]
    }
  }
  groupBoundingRect (childrens) {
    // let pageData = this.findPagePaints(this.boardData.current_pid)
    let rect = null
    childrens.map((gid) => {
      let childRect = this.boundingRect(gid)
      rect = rect || childRect
      rect.x1 = Math.min(rect.x1, childRect.x1)
      rect.y1 = Math.min(rect.y1, childRect.y1)
      rect.x2 = Math.max(rect.x2, childRect.x2)
      rect.y2 = Math.max(rect.y2, childRect.y2)
    })
    return rect
  }
}

export default PageStore;