// import { PaasRender } from './paasRender'
// import { CloudRender } from './cloudRender'
// 8fff55e8-43db-4641-9592-6926e2962047
import draw from "../../../util/draw"
import wbManager from "../../white-board-manager"
import eventRegistry from '../../../../common/event-registry'
class Render {
  constructor (painter, pageStore) {
    this.painter = painter
    this.pageStore = pageStore
  }
  /**
   * 
   */
  _drawAllPaint() {
    // this._emptyCanvas()
    if (this.pageStore) {
      let paintObjs = this.pageStore.findPagePaints(this.pageStore.getCurPage())
      paintObjs.map((item) => {
        this._drawPaint(item.type, item[item.type])
      })
    }
  }
   /**
   * (10)
   */
   _insertImage (n = 0) {
    if (n > 10) {
      console.warn("insertImage failed!");
      return
    }
    const convertPath = this.pageStore.getConvertFilePath()
    let rootPath = convertPath
    const curPage = this.pageStore.getCurPage()
    let fullPath = convertPath
    if (!String(fullPath).toLowerCase().includes('.jpg')) {
      fullPath = `${convertPath}/${curPage}.jpg`
    } else {
      rootPath = convertPath.substr(0, convertPath.lastIndexOf('/'))
    }
    if (wbManager.cacheFileUrlMap.get(rootPath) && wbManager.cacheFileUrlMap.get(rootPath).get(fullPath)) {
      if (this.painter.canvasEle) {
        this.painter._refreshRender()
      } else {
        console.warn("Invalid canvas element!");
      }
    } else {
      wbManager._getFileUrl(fullPath)
      setTimeout((() => {
        this._insertImage(++n)
      }).bind(this), 3000)
    }
  }
  /**
   * 
   */
  _emptyCanvas() {
    if (this.painter.canvasCtx) {
      this.painter.canvasCtx.clearRect(0, 0, this.painter.canvasWidth, this.painter.canvasHeight);
    }
  }
  /**
   * 
   * @param {*} type 
   * @param {*} obj 
   */
  _drawPaint (type, obj) {
    if (this.painter.canvasCtx) {
      try {
        draw[type](this.painter.canvasCtx, obj, this.painter.scaleRatio, this.painter.canvasEle)
      } catch (err) {
        console.error("Draw type: " + type + ", error: " + err);
      }
    }
  }
  drawAmendData (pageData, paintObj) {
    this._drawAllPaint()
    const paint = this.pageStore.findPaintByGid(pageData, paintObj[paintObj.type].gid)
    if (paint) {
      this._drawPaint(paint.type, paint[paint.type])
    }
  }
  /**
   * // 
   * @param {*} paintObj 
   */
  dealAmendData (paintObj) {
    let pageData = this.pageStore.findPagePaints(this.pageStore.getCurPage())
    if (paintObj[paintObj.type].action === 1) { // 
      this.pageStore.addPaintObj(pageData, paintObj)
      this.drawAmendData(pageData, paintObj)
    } else if (paintObj[paintObj.type].action === 2) { // 
      this.pageStore.amendPaintObj(pageData, paintObj)
      this.drawAmendData(pageData, paintObj)
    } else if (paintObj[paintObj.type].action === 3) { // 
      this.pageStore.deletePaintObj(pageData, paintObj[paintObj.type].gid)
      this._emptyCanvas()
      this._drawAllPaint()
    } else if (paintObj[paintObj.type].action === 4) {
      this.pageStore.repalcePaintObj(pageData, paintObj[paintObj.type].gid, paintObj[paintObj.type])
      this._drawAllPaint()
    }
  }

  /**
   * 
   * @param {*} pageObj 
   */
  pageChange (pageObj) {
    if (!this.pageStore) return
    const { cur_page } = pageObj
    if (cur_page === this.pageStore.getCurPage()) return
    this.pageStore.setCurPage(pageObj.cur_page)
    if (this.pageStore.getConvertFilePath()) {
      this._insertImage()
    }
    // 
    eventRegistry.trigger("shareOperation", {
      type: 'pageChange',
      mediaId: this.painter.mediaId, 
      curPage: this.pageStore.getCurPage()
    })
  }

  /**
   * 
   * @param {*} paintObj 
   */
  moveGraph (paintObj) {
    let pageData = this.pageStore.findPagePaints(paintObj.gmov.pid)
    paintObj.gmov.graphs.map((gid) => {
      this.pageStore.movePaintObj(pageData, gid, paintObj.gmov)
    })
    this._emptyCanvas()
    this._drawAllPaint()
  }
  // 
  drawBoundingRect (obj) {
    if (!obj) return
    this.painter.canvasCtx.beginPath()
    this.painter.canvasCtx.lineWidth = 1 // 
    this.painter.canvasCtx.strokeStyle = '#00ff00' // 

    let x1 = parseInt(obj.x1) * this.painter.scaleRatio
    let y1 = parseInt(obj.y1) * this.painter.scaleRatio;
    let x2 = parseInt(obj.x2) * this.painter.scaleRatio;
    let y2 = parseInt(obj.y2) * this.painter.scaleRatio;

    this.painter.canvasCtx.rect(x1, y1, (x2 - x1), (y2 - y1));
    this.painter.canvasCtx.closePath();
    this.painter.canvasCtx.stroke();
  }
}

export default Render