import eventRegistry from "../event-registry";
import InterceptorsManage from "./interceptorsManage";
/**
 * axiospromise websocket 
 * : config {
 *  eventName // 
 *  requestFn // websocket 
 *  retryConfig: {
 *    num // 
 *    max // 
 *  }
 * }
 *  interceptors.request.use()
 *  interceptors.respone.use()
 */

class HstRequest {
  constructor () {
    this.interceptors = {
      request: new InterceptorsManage(),
      response: new InterceptorsManage()
    } 
  }
  request (config)   {
    let chain = [this.sendMessge.bind(this),undefined];

    this.interceptors.request.handlers.forEach(interceptor => {
      chain.unshift(interceptor.onFulField,interceptor.onRejected);
    })

    this.interceptors.response.handlers.forEach(interceptor => {
      chain.push(interceptor.onFulField, interceptor.onRejected)
    })
    let promise = Promise.resolve(config);
    while (chain.length > 0){
      promise = promise.then(chain.shift(),chain.shift());
    }
    return promise;
  }

  sendMessge( config ) {
    return new Promise((resolve, reject) => {
      eventRegistry.on(config.eventName, function(res) {
        resolve({
          data: res,
          config
        });
      });
      config.requestFn()
    });
  }
}
export default HstRequest
